import * as React from 'react';
import { Grid, Tabs, Tab } from '@material-ui/core/';
import { useState } from 'react';

import { TableComponent } from 'Components/UI/TableComponent';

interface DocumentsProps {
  all_documents_table: any;
  uploaded_documents_table: any;
  missing_documents_table: any;
  documents_tab: any;
  all_data: any;
  all_filter: any;
  all_sort: any;
  uploaded_filter: any;
  uploaded_sort: any;
  missing_filter: any;
  missing_sort: any;
  applySortFilter: (params: any, tab: number, type: string) => void;
}

export const Documents: React.FC<DocumentsProps> = (
  props: DocumentsProps,
): JSX.Element => {
  const [selectedTab, setSelectedTab] = useState(0);

  function handleClickRow2() {

  }

  function handleFilter(filter: any) {
    props.applySortFilter(filter, selectedTab, 'filter');
  }

  function handleSort(sorting: any) {
    props.applySortFilter(sorting, selectedTab, 'sort');
  }
  
  return (
    <Grid container>
      <Grid item xs={12}>
        <Tabs
          value={selectedTab}
          onChange={(_event: React.ChangeEvent<{}>, value: number) => {
            setSelectedTab(value);
          }}
          aria-label="documenst tab"
        >
          {props.documents_tab.map(data => (
            <Tab
              label={data.label + ' (' + data.count + ')'}
              id={'view_member_documents_tab_' + data.count}
              aria-controls={'verify-member-tabpanel-' + data.count}
            />
          ))}
        </Tabs>
      </Grid>
      <Grid item xs={12} style={{ paddingLeft: '8px' }}>
        {selectedTab === 0 ? (
          <TableComponent
            id="document_all_table"
            rows={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['rows']
                : []
            }
            columns={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['columns']
                : []
            }
            message="No available data"
            onClickRow={handleClickRow2}
            disableSelect
            disableSearch
            columnFilteringEnabled={false}
            formattedColumns={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['formattedColumns']
                : {}
            }
            columnExtensions={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['columnExtensions']
                : []
            }
            filterExtensions={
              Object.keys(props.all_documents_table).length > 0
                ? props.all_documents_table['filterExtensions']
                : []
            }
            onFilterChange={handleFilter}
            onSortingChange={handleSort}
            defaultFilter={props.all_filter}
            defaultSort={props.all_sort}
          />
        ) : selectedTab === 1 ? (
          <TableComponent
            id="document_uploaded_table"
            rows={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['rows']
                : []
            }
            columns={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['columns']
                : []
            }
            message="No available data"
            onClickRow={handleClickRow2}
            disableSelect
            disableSearch
            columnFilteringEnabled={false}
            formattedColumns={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['formattedColumns']
                : {}
            }
            columnExtensions={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['columnExtensions']
                : []
            }
            filterExtensions={
              Object.keys(props.uploaded_documents_table).length > 0
                ? props.uploaded_documents_table['filterExtensions']
                : []
            }
            onFilterChange={handleFilter}
            onSortingChange={handleSort}
            defaultFilter={props.uploaded_filter}
            defaultSort={props.uploaded_sort}
          />
        ) : selectedTab === 2 ? (
          <TableComponent
            id="document_missing_table"
            rows={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['rows']
                : []
            }
            columns={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['columns']
                : []
            }
            message="No available data"
            onClickRow={handleClickRow2}
            disableSelect
            disableSearch
            columnFilteringEnabled={false}
            formattedColumns={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['formattedColumns']
                : {}
            }
            columnExtensions={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['columnExtensions']
                : []
            }
            filterExtensions={
              Object.keys(props.missing_documents_table).length > 0
                ? props.missing_documents_table['filterExtensions']
                : []
            }
            onFilterChange={handleFilter}
            onSortingChange={handleSort}
            defaultFilter={props.missing_filter}
            defaultSort={props.missing_sort}
          />
        ) : null}
      </Grid>
    </Grid>
  );
};

Documents.defaultProps = {
  all_data: {},
  all_documents_table: {},
  uploaded_documents_table: {},
  missing_documents_table: {},
  documents_tab: [],
  all_filter: [],
  uploaded_filter: [],
  missing_filter: [],
  all_sort: [],
  uploaded_sort: [],
  missing_sort: [],
};
