import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { DropdownChoice, ConfinementDetails } from 'Pages/CustomerCarePage/Models';


interface IProps {
    confinement: ConfinementDetails;
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    cptCodes: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshConfinementState: () => void;
}

export class ConfinementSection extends React.Component<IProps> {

    confinementTypes: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.confinementTypes = [
            { value: '1', label: 'Elective / Scheduled' },
            { value: '2', label: 'ER leading to confinement' }
        ];
    }

    render(): JSX.Element {
        const confinement = this.props.confinement;
        return (
            <div className='confinement-details'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>CONFINEMENT DETAILS</div>
                    <DetailedDropdown 
                        choices={this.props.facilities}
                        placeholder='Provider'
                        value={confinement.provider}
                        error={
                            this.props.createTicketAttemp && 
                            confinement.provider.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.confinement.provider = value;
                            this.props.refreshConfinementState();
                        }} />
                    <Grid container spacing={1}>
                        <Grid item xs={6}>
                            <DatePicker 
                                placeholder='Date Admitted' 
                                value={confinement.dateAdmitted}
                                onChange={(value: Date) => {
                                    this.props.confinement.dateAdmitted = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={6}>
                            <Dropdown 
                                placeholder='Kind of Confinement' 
                                choices={this.confinementTypes}
                                value={{ value: '', label: confinement.confinementKind}}
                                error={
                                    this.props.createTicketAttemp && 
                                    confinement.confinementKind === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.confinement.confinementKind = value.label;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <InputField 
                                placeholder='Room Type' 
                                value={confinement.roomType}
                                error={
                                    this.props.createTicketAttemp && 
                                    confinement.roomType === ''}
                                onChange={(value: string) => {
                                    this.props.confinement.roomType = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField 
                                placeholder='Cost Per Day' 
                                value={confinement.costPerDay}
                                detail={'PHP'}
                                isFloat={true}
                                onChange={(value: number) => {
                                    this.props.confinement.costPerDay = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={6}>
                            <Dropdown  
                                placeholder='Admitting Physician' 
                                choices={this.props.doctors}
                                value={confinement.doctor}
                                error={
                                    this.props.createTicketAttemp && 
                                    confinement.doctor.value === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.confinement.doctor = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={6}>
                            <DetailedDropdown 
                                placeholder='Initial Diagnosis' 
                                choices={this.props.cptCodes}
                                value={confinement.initialDiagnosis}
                                error={
                                    this.props.createTicketAttemp && 
                                    confinement.initialDiagnosis.value === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.confinement.initialDiagnosis = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField 
                                placeholder='Amount' 
                                value={confinement.amount}
                                detail={'PHP'}
                                isFloat={true}
                                onChange={(value: number) => {
                                    this.props.confinement.amount = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                    </Grid>
                </div>
                <div className='section' />
            </div>
        );
    }
}