/**
 *
 * App
 *
 * This component is the skeleton around the actual pages, and should only
 * contain code that should be seen on all pages. (e.g. navigation bar)
 */

import * as React from 'react';
import { Helmet } from 'react-helmet';
import { renderRoutes } from 'react-router-config';

import { Switch, Route } from 'react-router-dom';

import { Containers, Components } from '@hims/core';
import { createBrowserHistory } from 'history';
// import { HomePage } from 'Pages/Home/HomePage';


import { CreateTicketPage } from 'Pages/CustomerCarePage/CreateTicketPage/CreateTicketPage';
import { UpdateTicketPage } from 'Pages/CustomerCarePage/UpdateTicketPage/UpdateTicketPage';
import HomePage from 'Pages/Home/Loadable';
import { ResolveTicketPage } from 'Pages/CustomerCarePage/ResolveTicketPage/ResolveTicketPage';
import TicketListPage from 'Pages/TicketList/Loadable';
import { AdvanceSearch } from 'Pages/CustomerCarePage/AdvanceSearch/AdvanceSearch';
// import LoginPage from 'Pages/Login/LoginPage';
import { Dashboard } from 'Pages/Dashboard/Dashboard';

const history = createBrowserHistory();
const NotFoundPage = Containers.NotFoundPage;
// import ExitToAppIcon from '@material-ui/icons/ExitToApp';

let routes = [
  {
    path: '/home',
    component: HomePage,
  },
  {
    path: '/dashboard',
    component: Dashboard,
  },
  {
    path: '/',
    component: Dashboard,
    exact: true
  },
  {
    path: '/tickets',
    component: TicketListPage,
  },
  // {
  //   path: '/login',
  //   component: LoginPage
  // },
  {
    path: '/create-ticket',
    component: CreateTicketPage
  },
  {
    path: '/search',
    component: AdvanceSearch
  },
  {
    path: '/update-ticket/:caseId/:ticketId',
    component: UpdateTicketPage
  },
  {
    path: '/resolve-ticket/:caseId/:ticketId',
    component: ResolveTicketPage
  }
];
const prefix = '/customer-care';
routes.map(route => (route.path = `${prefix}${route.path}`));

export default function App() {

  return (
    <div>
      
      <Helmet
        titleTemplate='NEO - %s'
        defaultTitle='NEO'
      >
        <meta name='description' content='NEO application' />
      </Helmet>
      
      <div className='side-bar'>
        <Components.UI.PageLayout history={history} page={'customer care'} />
      </div>
      <div className='main'>
        <Switch>
          {renderRoutes(routes)}
          <Route path='' component={NotFoundPage} />
        </Switch>
      </div>
      
        
      {/* </Components.UI.PageLayout> */}
    </div>
  );
}