import * as React from 'react';
import { TableCell, TableRow } from '@material-ui/core';
import { ClientData, MemberFieldData } from 'Pages/CustomerCarePage/Models';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TextTab } from 'Pages/CustomerCarePage/Components/TextTab';

interface IProps {
    client: ClientData;
}

interface IStates {
    selectedTab: number;
}

export class MemberFieldsSection extends React.Component<IProps, IStates> {

    tabList = [
        { value: 1, label: 'Client-required' },
        { value: 2, label: 'Mandatory' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1
        };
    }

    filterByType = (fieldtype: string, memberFields: MemberFieldData[]) => {
        return memberFields.filter(field => field.type === fieldtype);
    }

    render() : JSX.Element {
        const memberFields = this.props.client.memberFields;
        const selectedTab = this.state.selectedTab;

        return (
            <div>
                <div>
                    <TextTab
                        tabList={this.tabList}
                        selectedTab={this.state.selectedTab}
                        tabStyle={(_index: number) => {
                            return { padding: '0 1rem' };
                        }}
                        tabLabel={(index: number) => {
                            return (
                                <div style={{width: '100%'}}>
                                    <div className='header-3' style={{margin: '6px 0'}}>
                                        {this.tabList[index].label}
                                    </div>
                                </div>
                            );
                        }}
                        setSelectedTab={(value: number) => {
                            this.setState({selectedTab: value});
                        }} />
                </div>
                <div className='sub-section'>
                    {memberFields ? (
                        <CustomTable
                            className='custom-table'
                            rows={this.filterByType(this.tabList[selectedTab - 1].label, memberFields)}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: MemberFieldData, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    ) : null}
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        
        return (
            <>
            <TableCell>Required?</TableCell>
            <TableCell>Field</TableCell>
            <TableCell>Input Type</TableCell>
            <TableCell>Data Type</TableCell>
            </>
        );
    }

    renderTableRow(field: MemberFieldData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{field.isRequired ? 'Yes' : 'No'}</TableCell>
                <TableCell>{field.fieldName}</TableCell>
                <TableCell>{field.inputType}</TableCell>
                <TableCell>{field.dataType}</TableCell>
            </TableRow>
        );
    }
}