import * as React from 'react';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { Grid } from '@material-ui/core';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';

interface IProps {
    client: ClientData;
} 

export class BillingSection extends React.Component<IProps> {

    render() : JSX.Element {
        const billing = this.props.client.servicingInstructions ? 
                        this.props.client.servicingInstructions.billing : 
                        undefined;

        const softCopy = billing && 
                         billing.sendSoaFormats ?
                         billing.sendSoaFormats.find(i => i.format === 'soft_copy') :
                         undefined;

        return (
            <div className='sub-section'>
                <div className='sub-section'>
                    <div className='text'>Additional fields to be included in SOA attachment</div>
                    <CustomCheckbox
                        label='Site'
                        disabled
                        value={billing && billing.additionalFieldsSoa.includes('site') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Sub-company'
                        disabled
                        value={billing && billing.additionalFieldsSoa.includes('sub-company') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Cost Center'
                        disabled
                        value={billing && billing.additionalFieldsSoa.includes('cost center') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Endorsement / Batch Number'
                        disabled
                        value={billing && billing.additionalFieldsSoa.includes('batch no') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Employee No. / Employee ID'
                        disabled
                        value={billing && billing.additionalFieldsSoa.includes('employee no') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Cost c/o company / Employer'
                        disabled
                        value={billing && billing.additionalFieldsSoa.includes('cost care of') ? true : false}
                        onChange={(_value: boolean) => {}} />
                </div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <div className='text'>SOAs Generation</div>
                            {billing && billing.soaGeneration ? (
                                <div style={{boxShadow: '0px 3px 6px #00000029'}}>
                                    {billing.soaGeneration.map((i, index) => {
                                        const text = 
                                            i === 'site' ? 'Site' :
                                            i === 'sub-company' ? 'Sub-company' :
                                            i === 'cost-center' ? 'Cost Center' :
                                            i === 'batch-no' ? 'Endorsement / Batch Number' :
                                            i === 'employee-no' ? 'Employee No. / Employee ID' :
                                            i === 'alphabetical' ? 'Alphabetical' :
                                            i === 'effectivity-date' ? 'Effectivity Date' :
                                            i === 'company-paid-dependents' ? 'Company-Paid Dependents' :
                                            i === 'employee-paid-dependents' ? 'Employee-Paid Dependents' :
                                            i === 'principal-employees' ? 'Employees (Principal)' :
                                            i === 'member-plan-type' ? 'Member Plan Type' : '';
                                        const backgroundColor = (index % 2 === 1 ? '#FFFFFF' : '#F6F7FA');
                                        return (
                                            <div style={{backgroundColor: backgroundColor, padding: '8px'}}>
                                                {text}
                                            </div>
                                        );
                                    })}
                                </div>
                            ) : null}
                        </Grid>
                        <Grid item xs={6}>
                        <div className='text'>SOAs Sorting</div>
                            {billing && billing.soaGeneration ? (
                                <div style={{boxShadow: '0px 3px 6px #00000029'}}>
                                    {billing.soaSorting.map((i, index) => {
                                        const text = 
                                            i === 'site' ? 'Site' :
                                            i === 'sub-company' ? 'Sub-company' :
                                            i === 'cost-center' ? 'Cost Center' :
                                            i === 'batch-no' ? 'Endorsement / Batch Number' :
                                            i === 'employee-no' ? 'Employee No. / Employee ID' :
                                            i === 'alphabetical' ? 'Alphabetical' :
                                            i === 'effectivity-date' ? 'Effectivity Date' :
                                            i === 'company-paid-dependents' ? 'Company-Paid Dependents' :
                                            i === 'employee-paid-dependents' ? 'Employee-Paid Dependents' :
                                            i === 'principal-employees' ? 'Employees (Principal)' :
                                            i === 'member-plan-type' ? 'Member Plan Type' : '';
                                        const backgroundColor = (index % 2 === 1 ? '#FFFFFF' : '#F6F7FA');
                                        return (
                                            <div style={{backgroundColor: backgroundColor, padding: '8px'}}>
                                                {text}
                                            </div>
                                        );
                                    })}
                                </div>
                            ) : null}
                        </Grid>
                    </Grid>
                </div>
                <div className='sub-section'>
                    <Grid container>
                        <Grid item xs={6}>
                            <InputField
                                placeholder='Employee enrollment'
                                disabled
                                value={billing && billing.addressSoaTo ? billing.addressSoaTo : ''}
                                onChange={(_value: string) => {}} />
                            <InputField
                                placeholder='Send SOA to'
                                disabled
                                value={billing && billing.sendSoaTo ? billing.sendSoaTo : ''}
                                onChange={(_value: string) => {}} />
                        </Grid>
                    </Grid>
                </div>
                <div className='sub-section'>
                    <div className='text'>Send SOA on specific days only</div>
                    <CustomCheckbox
                        label='Monday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays && 
                            billing.sendSoaDays.includes('monday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Tuesday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays && 
                            billing.sendSoaDays.includes('tuesday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Wednesday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays && 
                            billing.sendSoaDays.includes('wednesday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Thursday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays && 
                            billing.sendSoaDays.includes('thursday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Friday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays &&
                            billing.sendSoaDays.includes('friday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Saturday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays &&
                            billing.sendSoaDays.includes('saturday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Sunday'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaDays &&
                            billing.sendSoaDays.includes('sunday') ? true : false}
                        onChange={(_value: boolean) => {}} />
                </div>
                <div className='sub-section'>
                    <div className='text'>Send SOA in the following formats</div>
                    <CustomCheckbox
                        label='Hard Copy'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaFormats &&
                            billing.sendSoaFormats.find(i => i.format === 'hard copy') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Soft Copy'
                        disabled
                        value={
                            billing && 
                            billing.sendSoaFormats &&
                            billing.sendSoaFormats.find(i => i.format === 'soft_copy') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <Grid container>
                        <Grid item xs={1} />
                        <Grid item xs={5}>
                            <CustomCheckbox
                                label='Email'
                                disabled
                                value={
                                    softCopy &&
                                    softCopy.options &&
                                    softCopy.options.includes('email') ? true : false}
                                onChange={(_value: boolean) => {}} />
                            <CustomCheckbox
                                label='Client Portal'
                                disabled
                                value={
                                    softCopy &&
                                    softCopy.options &&
                                    softCopy.options.includes('client portal') ? true : false}
                                onChange={(_value: boolean) => {}} />
                        </Grid>
                    </Grid>
                </div>
                <div className='sub-section'>
                    <div className='text'>CM Application</div>
                    <CustomCheckbox
                        label='Separate SOA and CM'
                        disabled
                        value={
                            billing && 
                            billing.cmApplication && 
                            billing.cmApplication.includes('separate soa and cm') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Automatic application / offset against current SOAs'
                        disabled
                        value={
                            billing && 
                            billing.cmApplication && 
                            billing.cmApplication.includes('automatic application') ? true : false}
                        onChange={(_value: boolean) => {}} />
                </div>
                <div className='sub-section'>
                    <div className='text'>Additional requirements</div>
                    <CustomCheckbox
                        label='Billing validation required prior to sending final SOA'
                        disabled
                        value={
                            billing && 
                            billing.additionalRequirements && 
                            billing.additionalRequirements.includes('billing validation') ? true : false}
                        onChange={(_value: boolean) => {}} />
                    <CustomCheckbox
                        label='Premium Refund not to be released until full payment of premium'
                        disabled
                        value={
                            billing && 
                            billing.additionalRequirements && 
                            billing.additionalRequirements.includes('premium refund') ? true : false}
                        onChange={(_value: boolean) => {}} />
                </div>
            </div>
        );
    }
}