import * as React from 'react';
import { ClientData, ContractData } from 'Pages/CustomerCarePage/Models';
import { TextTab } from 'Pages/CustomerCarePage/Components/TextTab';
import { API } from 'Pages/TicketList/API';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TableRow, TableCell } from '@material-ui/core';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';

interface IProps {
    client: ClientData;
}

interface IStates {
    selectedTab: number;
    contracts?: ContractData[];
    proposals?: ContractData[];
}

export class HistorySection extends React.Component<IProps, IStates> {

    tabList = [
        { value: 1, label: 'Contracts' },
        { value: 2, label: 'Proposals' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1
        };
    }

    componentDidMount() {
       this.loadData('contracts');
    }

    componentWillUpdate(_props: IProps, state: IStates) {
        if (this.state.selectedTab != state.selectedTab) {
            this.loadData(state.selectedTab === 1 ? 'contracts' : 'proposals');
        }
    }

    loadData = (type: 'contracts' | 'proposals') => {
        API.get(`clients/${this.props.client.id}/type/${type}`,
            (response) => {
                console.log(response);
                if (type === 'contracts') {
                    this.setState({contracts: Payloads.getContractData(response.data)});
                } else if (type === 'proposals') {
                    this.setState({proposals: Payloads.getContractData(response.data)});
                }
            },
            (error) => {
                console.log(error);
            });
    }

    render() : JSX.Element {

        return (
            <div>
                <div>
                    <TextTab
                        tabList={this.tabList}
                        selectedTab={this.state.selectedTab}
                        tabStyle={(_index: number) => {
                            return { padding: '0 1rem' };
                        }}
                        tabLabel={(index: number) => {
                            return (
                                <div style={{width: '100%'}}>
                                    <div className='header-3' style={{margin: '6px 0'}}>
                                        {this.tabList[index].label}
                                    </div>
                                </div>
                            );
                        }}
                        setSelectedTab={(value: number) => {
                            this.setState({selectedTab: value});
                        }} />
                </div>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    {this.state.selectedTab === 1 && this.state.contracts ? (
                        this.renderContractTable(this.state.contracts)
                    ) : this.state.selectedTab === 2 && this.state.proposals ? (
                        this.renderContractTable(this.state.proposals)
                    ) : (
                        <Loading height='10rem' />
                    )}
                </div>
            </div>
        );
    }

    renderContractTable(data: ContractData[]): JSX.Element {
        return (
            <CustomTable
                rows={data}
                renderHeader={() => this.renderTableHeader()}
                renderRow={(rowData: any, index: number) => 
                    this.renderTableRow(rowData, index)} />
        )
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            {this.renderHeaderColumn('fiterName', 'Contract')}
            {this.renderHeaderColumn('filterBenefits', 'Contract No.')}
            {this.renderHeaderColumn('filterPrincipals', 'Contract Effectivity Date', '200px')}
            {this.renderHeaderColumn('filterDependents', 'Contract Type')}
            {this.renderHeaderColumn('filterDateCreated', 'Version')}
            {this.renderHeaderColumn('filterDateUpdated', 'Modified Date')}
            {this.renderHeaderColumn('filterVersion', 'Status')}
            </>
        );
    }

    renderTableRow(contract: ContractData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const startDate = contract.startDate ? GlobalFunction.toShortDateString(contract.startDate) : '---';
        const updateDate = contract.updateDate ? GlobalFunction.toShortDateString(contract.updateDate) : '---';

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{contract.name}</TableCell>
                <TableCell>{contract.contractNo || '---'}</TableCell>
                <TableCell>{startDate}</TableCell>
                <TableCell>{contract.type || '---'}</TableCell>
                <TableCell>{contract.version || '---'}</TableCell>
                <TableCell>{updateDate}</TableCell>
                <TableCell>{contract.status || '---'}</TableCell>
            </TableRow>
        );
    }

    renderHeaderColumn(attrName: string, headerText: string, width?: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width ? width : '150px'}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            // this.sortFacilities(attrName, true);
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        // @ts-ignore
                        this.setState({[attrName]: value});
                    }} />
            </TableCell>
        );
    }
}