//#endregion Global Imports

let radioSelected = false;
let selectedValue = '';
let clickCell = false;
let clickValue = {};

const cellSelectStyles = makeStyles({
  select: {
    backgroundColor: 'transparent',
  },
});

const textFieldStyles = makeStyles({
  root: {
    '& div.MuiInputBase-root': {
      backgroundColor: 'transparent',
    },
  },
});

const checkboxStyles = makeStyles({
  root: {
    '&.Mui-checked': {
      color: '#3AB77D',
    },
  },
});

const Select = ({ id, value, items }: any) => {
  const cellSelectClasses = cellSelectStyles();
  return (
    <MaterialUI.Select
      id={id}
      classes={cellSelectClasses}
      value={value}
      style={{ width: '100%', backgroundColor: 'transparent' }}
      renderValue={(selected: any) => {
        if (selected !== null) {
          const value: any = items.find((item: any) => {
            return item.value === selected;
          });
          if (value) {
            return value.name;
          } else {
            return '';
          }
        }
        return '';
      }}
    >
      {items.map((item: any) => (
        <MaterialUI.MenuItem key={item.value} value={item.value}>
          {item.name}
        </MaterialUI.MenuItem>
      ))}
    </MaterialUI.Select>
  );
};

const TableFilterCell = ({
  filter,
  onFilter,
  filteringEnabled,
  classes,
}: any) => {
  if (filteringEnabled) {
    return (
      <MaterialUI.TableCell
        style={{ padding: '0px 10px 8px 10px' }}
        classes={classes}
      >
        <MaterialUI.OutlinedInput
          classes={classes}
          style={{ width: '100%' }}
          value={filter ? filter.value : ''}
          labelWidth={0}
          onChange={e =>
            onFilter(e.target.value ? { value: e.target.value } : null)
          }
          placeholder=""
        />
      </MaterialUI.TableCell>
    );
  } else {
    // return null;
    return (
      <MaterialUI.TableCell
        style={{ padding: '0px 10px 8px 10px' }}
        classes={classes}
      ></MaterialUI.TableCell>
    );
  }
};

const TableRadio = ({
  style,
  selected,
  onToggle,
  classes,
  ...restProps
}: any) => {
  return (
    <MaterialUI.TableCell
      padding="checkbox"
      style={style}
      classes={classes}
      {...restProps}
    >
      <MaterialUI.Radio
        classes={classes}
        checked={selected}
        onClick={e => {
          e.stopPropagation();
          onToggle();
        }}
      />
    </MaterialUI.TableCell>
  );
};

const TableCheckBox = ({
  style,
  selected,
  onToggle,
  classes,
  ...restProps
}: any) => {
  return (
    <MaterialUI.TableCell
      padding="checkbox"
      style={style}
      classes={classes}
      {...restProps}
    >
      <MaterialUI.Checkbox
        icon={<FontAwesomeIcon icon={faCheckSquare} />}
        checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
        classes={classes}
        checked={selected}
        onClick={e => {
          e.stopPropagation();
          onToggle();
        }}
      />
    </MaterialUI.TableCell>
  );
};

const TableSearchPanel = ({ value, onValueChange }: any) => {
  return (
    <MaterialUI.OutlinedInput
      value={value ? value : ''}
      labelWidth={0}
      onChange={e => onValueChange(e.target.value ? e.target.value : null)}
      placeholder="Search a benefit or benefit category"
      fullWidth
      startAdornment={
        <MaterialUI.InputAdornment position="start">
          <FontAwesomeIcon icon={faSearch} />
        </MaterialUI.InputAdornment>
      }
    />
  );
};

const DefaultTableToolbar = ({ children }: any) => {
  return <MaterialUI.Toolbar disableGutters={true} children={children} />;
};

const SortingIcon = ({ direction }: any) =>
  direction === 'asc' ? (
    <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortUp} />
  ) : (
    <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSortDown} />
  );

const TableSortLabel = ({ column, onSort, children, direction }: any) => {
  if (
    column.sortingEnabled !== undefined &&
    column.sortingEnabled !== null &&
    column.sortingEnabled === false
  ) {
    return <span>{column.title}</span>;
  } else {
    return (
      <MaterialUI.TableSortLabel hideSortIcon={true} onClick={onSort}>
        {children}
        {direction ? (
          <SortingIcon direction={direction} />
        ) : (
          <FontAwesomeIcon style={{ marginLeft: '10px' }} icon={faSort} />
        )}
      </MaterialUI.TableSortLabel>
    );
  }
};

const EmptyCellFormatter = ({ value }: any) => {
  if (value === '' || value === undefined || value === null) {
    return (
      <FontAwesomeIcon
        style={{ color: '#FD5451', fontSize: '16px' }}
        icon={faExclamationCircle}
      />
    );
  } else {
    return value;
  }
};

const EmptyCellDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={EmptyCellFormatter} {...props} />
);

const RequiredFormatter = ({ row, value }: any) => {
  if (
    row['required'] === true &&
    (value === '' || value === undefined || value === null)
  ) {
    return (
      <FontAwesomeIcon
        style={{ color: '#FD5451', fontSize: '16px' }}
        icon={faExclamationCircle}
      />
    );
  } else if (
    row['required'] === false &&
    (value === '' || value === undefined || value === null)
  ) {
    return 'N/A';
  } else {
    return value;
  }
};

const RequiredDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={RequiredFormatter} {...props} />
);

const RequiredCheckboxFormatter = ({ column, row, value }: any) => {
  const classes = checkboxStyles();
  return (
    <div>
      <MaterialUI.FormControlLabel
        control={
          <MaterialUI.Checkbox
            checked={value === true || value === 'true' ? true : false}
            value={value}
            style={{ padding: '5px' }}
            className={classes.root}
            color="primary"
            onChange={e => {
              clickCell = true;
              clickValue = {
                value: e.target.checked,
                name: column['name'],
                id: row['id'],
              };
            }}
          />
        }
        label=""
      />
    </div>
  );
};

const RequiredCheckboxDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={RequiredCheckboxFormatter} {...props} />
);

const ReadOnlyCheckboxFormatter = ({ value }: any) => {
  const classes = checkboxStyles();
  return (
    <div>
      <MaterialUI.FormControlLabel
        control={
          <MaterialUI.Checkbox
            checked={value === true || value === 'true' ? true : false}
            value={value}
            disabled={true}
            style={{ padding: '5px' }}
            className={classes.root}
            color="primary"
            onChange={() => {
            }}
          />
        }
        label=""
      />
    </div>
  );
};

const ReadOnlyCheckboxDataTypeProvider = (props: any) => (
  <DataTypeProvider formatterComponent={ReadOnlyCheckboxFormatter} {...props} />
);

