import * as React from 'react';
import { CRUDTable } from 'Pages/CustomerCarePage/UpdateTicketPage/Components/CRUDTable';
import { Grid, TableCell } from '@material-ui/core';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';
import { ItemData } from '../UpdateTicketProps';

interface IProps {
    items: ItemData[];
    state: ItemData;
    refreshItemsStateChange: () => void;
}

export class OtherDetailsSection extends React.Component<IProps> {

    itemTypes: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.itemTypes = [
            { value: '1', label: 'Medicine' },
            { value: '2', label: 'Non-coverable' },
            { value: '3', label: 'Rooms' },
            { value: '4', label: 'Miscellaneous' }
        ]
    }

    onFormatTableHeader = (colIndex: number, value: any) => {
        return (
            colIndex === 2 ? (
                <TableCell style={{textAlign: 'right', minWidth: '200px'}}>{value}</TableCell>
            ) : (
                <TableCell style={{minWidth: '300px'}}>{value}</TableCell>
            )
        );
    }

    onFormatCRUDTable = (colIndex: number, value: any) => {
        return (
            colIndex === 1 ? (
                value.label
            ) : colIndex === 2 ? (
                <div style={{textAlign: 'right'}}>
                    {GlobalFunction.formatNumber(value, true) + ' PHP'}
                </div>
            ) : (value)
        );
    }

    render(): JSX.Element {
        console.log(this.props.items);
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Add Other Details</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            <InputField 
                                placeholder='Item'
                                value={this.props.state.name}
                                // error={this.props.state.name.trim().length == 0}
                                onChange={(value: string) => {
                                    this.props.state.name = value;
                                    this.props.refreshItemsStateChange();
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            <Dropdown 
                                placeholder='Type'
                                value={this.props.state.type}
                                choices={this.itemTypes}
                                // error={this.props.state.type.value === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.state.type = value;
                                    this.props.refreshItemsStateChange();
                                }}/>
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField 
                                placeholder='Amount'
                                detail='PHP'
                                isFloat={true}
                                value={this.props.state.amount}
                                // error={this.props.state.amount == 0}
                                onChange={(value: number) => {
                                    this.props.state.amount = value;
                                    this.props.refreshItemsStateChange();
                                }}/>
                        </Grid>
                    </Grid>
                    <div style={{paddingTop: '5px'}} />
                    {this.props.state.name.trim().length != 0 &&
                     this.props.state.type.value !== '' &&
                     this.props.state.amount != 0 ? (
                        <TextButton 
                            label='Add item' 
                            icon={faPlus}
                            onClick={() => {
                                var state = this.props.state;
                                const tempData = {
                                    name: state.name,
                                    type: { value: state.type.value, label: state.type.label },
                                    amount: state.amount
                                }
                                this.props.items.unshift(tempData);
                                this.props.state.name = '';
                                this.props.state.type = { value: '', label: '' };
                                this.props.state.amount = 0;
                                this.props.refreshItemsStateChange();
                            }}/>
                    ) : null}
                </div>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    <CRUDTable 
                        className='custom-table'
                        placeholder='OTHER DETAILS'
                        
                        cols={['Item', 'Type', 'Amount']} 
                        rows={this.props.items}
                        onSaveEditRow={(index: number, data: any) => {
                            // this.props.editItem(index, data);
                            this.props.items[index] = data;
                            this.props.refreshItemsStateChange();
                        }}
                        onDeleteRow={(index: number) => {
                            // this.props.deleteItem(index);
                            this.props.items.splice(index, 1);
                            this.props.refreshItemsStateChange();
                        }}
                        onHeaderFormat={(colIndex: number, colName: string) => {
                            return this.onFormatTableHeader(colIndex, colName);
                        }}
                        onFormat={(_rowIndex: number, colIndex: number, value: any) => {
                            return this.onFormatCRUDTable(colIndex, value);
                        }}
                        editField={(colIndex: number, currentVal: any, onChange: any) => {
                            if (colIndex === 0) {
                                return (
                                    <InputField
                                        value={currentVal}
                                        onChange={(value: string) => onChange(value)}/>
                                );
                            } else if (colIndex === 1) {
                                return (
                                    <Dropdown
                                        choices={this.itemTypes}
                                        value={currentVal}
                                        onChange={(value: DropdownChoice) => onChange(value)}/>
                                );
                            } else {
                                return (
                                    <DetailedNumberField
                                        detail='PHP'
                                        isFloat={true}
                                        value={currentVal}
                                        onChange={(value: number) => onChange(value)}/>
                                );
                            }
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }
}