import * as React from 'react';
import { PartialClientData } from 'Pages/CustomerCarePage/Models';

interface IProps {
    clients: PartialClientData[];
    searchText: string;
    onSelectClient: (clientMongoId: string) => void;
}

export class SearchedClientsList extends React.Component<IProps> {

    getExtractedClients = (filter: string) => {
        return (filter.trim().length > 0 ? (
            this.props.clients
                .filter(client => client.clientId)
                .filter(client => client.clientId.includes(filter))
        ) : []);
    }

    render(): JSX.Element {
        return (
            <div className='mini-ticket-modals'>
                {this.getExtractedClients(this.props.searchText).map(client => {
                    return (
                        <>
                        {this.renderMemberShortDetail(client)}
                        <div className='line-border' />
                        </>
                    );
                })}
            </div>
        );
    }

    renderMemberShortDetail(client: PartialClientData): JSX.Element {

        return (
            <div 
                className='small-section mini-ticket-modal'
                onClick={() => this.props.onSelectClient(client.clientId)}>
                <div className='text' />
                <div className='text'>
                    <div className='circle' style={{backgroundColor: '#3AB77D'}} />
                    <div style={{display: 'inline-block'}}>
                        <div className='header-2'>{client.clientId}</div>
                    </div>

                </div>
                
                <div className='text'>{client.clientName}</div>
            </div>
        );
    }
}