import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { API } from 'Pages/CustomerCarePage/API';
import { TicketData } from 'Pages/CustomerCarePage/Models';

interface IProps {
    caseId: string;
    ticket: TicketData;
    isEscalateModalOpen: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

interface IStates {
    notes: string;
}

export class EscalateTicketModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            notes: ''
        };
    }

    onEscalate = () => {
        const ticket = this.props.ticket;

        const body = {
            button_pressed: 'ESCALATE',
            assigned_by: ticket.assignee.name,
            assign_remarks: this.state.notes
        };

        const patchBody = {
            assign_to: '---' // TODO: This is still static
        };

        API.patch(`customer-care/tickets/${this.props.ticket.id}`, patchBody, () => {
            Processmaker.shortcutCall('PUT', `cases/${this.props.caseId}/variable`, body, () => {
                Processmaker.shortcutCall('PUT', `cases/${this.props.caseId}/route-case`, {}, () => {
                    this.props.onSuccess();
                });
            });
        });
    }

    render(): JSX.Element {
        return (
            <CustomModal
                className='modal'
                isOpen={this.props.isEscalateModalOpen}
                handleClose={() => this.props.onClose()}
                header='Escalate Ticket'
                content={
                    <>
                    <div style={{padding: '0.5rem 0'}}>
                        {`This ticket will be automatically assigned to your supervisor for ticket updates.`}
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <InputArea 
                            placeholder='Notes'
                            defaultRows={4}
                            value={this.state.notes}
                            onChange={(value: string) => {
                                this.setState({notes: value});
                            }} />
                    </div>
                    </>
                }
                footer={
                    <>
                    <CustomButton
                        text='Cancel'
                        variant='outlined'
                        onClick={() => {
                            this.props.onClose()
                        }} />
                    <span style={{padding: '1rem'}} />
                    <CustomButton 
                        text='Escalate'
                        variant='contained'
                        onClick={() => {
                            this.onEscalate();
                        }} />
                    </>
                } />
        );
    }
}