import * as React from 'react';
import './component-style.scss';
import MomentUtils from '@date-io/moment';
import { KeyboardDatePicker, MuiPickersUtilsProvider, MaterialUiPickersDate } from '@material-ui/pickers';

interface IProps {
    placeholder?: string;
    value: Date | null;
    minDate?: Date;
    disabled?: boolean;
    onChange: (value: Date | null) => void;
}

export class DatePicker extends React.Component<IProps> {

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.value !== props.value)
        );
    }

    render(): JSX.Element {
        return (
            <div className='date-picker'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <KeyboardDatePicker
                        className={this.props.disabled ? 'text-field-disabled text-field' : 'text-field'}
                        disabled={this.props.disabled}
                        variant='dialog'
                        format='MM/DD/YYYY'
                        minDate={this.props.minDate ? this.props.minDate : new Date(1900, 1, 1)}
                        value={this.props.value}
                        onChange={(date: MaterialUiPickersDate | null) => {
                            this.props.onChange(date ? date.toDate() : null);
                        }} />
                </MuiPickersUtilsProvider>
            </div>
        );
    }
}