import * as React from 'react';
import { ClientData, SimpleTicketData } from '../Models';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { Loading } from '../Components/Loading';
import { CustomTable } from '../Components/CustomTable';
import { GlobalFunction } from '../GlobalFunction';

interface IProps {
    client: ClientData;
    recentTickets?: SimpleTicketData[];
    linkedTickets?: SimpleTicketData[];
    getLinkedTicketsData: (clientId: string) => void;

    disableLinkedTickets?: boolean;
    loadingRecentTickets: boolean;
    loadingLinkedTickets: boolean;
}

export class ClientDataSection extends React.Component<IProps> {

    render(): JSX.Element {
        const client = this.props.client;
        const contract = client.contract;

        if (!this.props.linkedTickets && !this.props.disableLinkedTickets) {
            this.props.getLinkedTicketsData(client.corporateAccountNo);
        }

        const mainAddress = client.mainOfficeAddress;
        const location = mainAddress ? `${mainAddress.barangay}, ${mainAddress.city}, ${mainAddress.region}` : '';
        const startDate = contract && contract.startDate ? GlobalFunction.toShortDateString(contract.startDate) : '---';
        const endDate = contract && contract.endDate ? GlobalFunction.toShortDateString(contract.endDate) : '---';
        
        return (
            <div className='section'>
                <div className='circle' style={{backgroundColor: '#3AB77D'}} />
                <div style={{display: 'inline-block'}}>
                    <div className='header-2'>{client.corporateAccountNo}</div>
                </div>
                <div className='text'>{client.registeredName}</div>
                <Grid container>
                    <Grid item className='sub-section-2' xs={12}>
                        <div className='light-text'>CARD NAME</div>
                        <div className='small-text'>{client.cardName}</div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={6}>
                        <div className='light-text'>EFFECTIVITY DATE</div>
                        <div className='small-text'>{startDate}</div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={6}>
                        <div className='light-text'>RENEWAL DATE</div>
                        <div className='small-text'>
                            {false ? ( // Renewal Date
                                GlobalFunction.toShortDateString('')
                            ) : '---*'}
                        </div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={6}>
                        <div className='light-text'>EXPIRY DATE</div>
                        <div className='small-text'>{endDate}</div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={6}>
                        <div className='light-text'>EXTENSION DATE</div>
                        <div className='small-text'>
                            {false ? ( // Extension Date
                                GlobalFunction.toShortDateString('')
                            ) : '---*'}
                        </div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={12}>
                        <div className='light-text'>SALES CHANNEL</div>
                        <div className='small-text'>
                            {client.salesChannel ? client.salesChannel.name : '---'}
                        </div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={12}>
                        <div className='light-text'>LOCATION</div>
                        <div className='small-text'>{location}</div>
                    </Grid>
                    <Grid item className='sub-section-2' xs={12}>
                        <div className='light-text'>CONTACT PERSON AND NO.</div>
                        <div className='small-text'>
                            {client.mainOfficeAddress && client.mainOfficeAddress.contactPersons.length > 0 ? (
                                `${client.mainOfficeAddress.contactPersons[0].name}: ${client.mainOfficeAddress.contactPersons[0].contactNo}`
                            ) : '---'}
                        </div>
                    </Grid>
                </Grid>
                <div className='sub-section-2'>
                    {this.props.loadingRecentTickets ? (
                        <Loading height='2rem' />
                    ) : (
                        this.props.recentTickets ? (
                            <>
                            <div className='header-2'>RECENT TICKETS</div>
                            <CustomTable
                                paginationClass='custom-pagination-small'
                                className='custom-table-small'
                                labelRowsPerPage='Rows:'
                                rows={this.props.recentTickets}
                                rowsPerPageOptions={[5, 10, 25]}
                                renderHeader={() => this.renderRecentTicketHeader()}
                                renderRow={(rowData: SimpleTicketData, index: number) =>
                                    this.renderRecentTicketRow(rowData, index)} />
                            </>
                        ) : null
                    )}
                </div>
                {/* <div className='sub-section-2'>
                    {this.props.loadingLinkedTickets ? (
                        <Loading height='2rem' />
                    ) : (
                        this.props.linkedTickets ? (
                            <>
                            <div className='header-2'>LINKED TICKETS</div>
                            <CustomTable
                                paginationClass='custom-pagination-small'
                                className='custom-table-small'
                                labelRowsPerPage='Rows:'
                                rows={this.props.linkedTickets}
                                rowsPerPageOptions={[5, 10, 25]}
                                renderHeader={() => this.renderLinkedTicketsHeader()}
                                renderRow={(rowData: SimpleTicketData, index: number) =>
                                    this.renderLinkedTicketsRow(rowData, index)} />
                            </>
                        ) : null
                    )}
                </div> */}
            </div>
        );
    }

    renderRecentTicketHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '70px', fontSize: '10px'}}>Ticket ID</TableCell>
            <TableCell style={{fontSize: '10px'}}>Status</TableCell>
            <TableCell style={{fontSize: '10px'}}>Created</TableCell>
            <TableCell style={{fontSize: '10px'}}>Updated</TableCell>
            </>
        );
    }

    renderRecentTicketRow(ticket: SimpleTicketData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor, fontSize: '10px'}}>
                <TableCell>{ticket.ticketNo}</TableCell>
                <TableCell>{ticket.status}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(ticket.dateCreated)}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(ticket.dateUpdated)}</TableCell>
            </TableRow>
        );
    }

    renderLinkedTicketsHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '70px', fontSize: '10px'}}>Ticket ID</TableCell>
            <TableCell style={{fontSize: '10px'}}>Status</TableCell>
            <TableCell style={{fontSize: '10px'}}>Created</TableCell>
            <TableCell style={{fontSize: '10px'}}>Updated</TableCell>
            </>
        );
    }

    renderLinkedTicketsRow(ticket: SimpleTicketData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor, fontSize: '10px'}}>
                <TableCell>{ticket.ticketNo}</TableCell>
                <TableCell>{ticket.status}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(ticket.dateCreated)}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(ticket.dateUpdated)}</TableCell>
            </TableRow>
        );
    }
}