import * as React from 'react';
import { ClientData, ReportorialData } from 'Pages/CustomerCarePage/Models';
import { TableCell, TableRow } from '@material-ui/core';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';

interface IProps {
    client: ClientData;
}

interface IStates {
    reportorialIsUpdated: boolean;
}

export class ReportorialSection extends React.Component<IProps, IStates> {

    reportorialData: ReportorialData[] = [
        { available: false, frequency: 'yearly', reportType: 'summary of claims' },
        { available: false, frequency: 'yearly', reportType: 'top facilities' },
        { available: false, frequency: 'yearly', reportType: 'top illnesses' },
        { available: false, frequency: 'yearly', reportType: 'detailed utilization' },
        { available: false, frequency: 'yearly', reportType: 'detailed account renewal status report' },
        { available: false, frequency: 'yearly', reportType: 'summary of account renewal status report' },
        { available: false, frequency: 'yearly', reportType: 'account analysis report by industry' },
        { available: false, frequency: 'yearly', reportType: 'account analysis report by segmentation' },
        { available: false, frequency: 'yearly', reportType: 'active members list' },
        { available: false, frequency: 'yearly', reportType: 'masterlist' },
        { available: false, frequency: 'yearly', reportType: 'card summary report' },
        { available: false, frequency: 'yearly', reportType: 'card replacement report' },
        { available: false, frequency: 'yearly', reportType: 'account transaction report' },
        { available: false, frequency: 'yearly', reportType: 'summary of claims report' },
        { available: false, frequency: 'yearly', reportType: 'top utilization report' },
        { available: false, frequency: 'yearly', reportType: 'detailed utilization report' },
        { available: false, frequency: 'yearly', reportType: 'account renewal status report' },
        { available: false, frequency: 'yearly', reportType: 'account analysis report' },
        { available: false, frequency: 'yearly', reportType: 'account segmentation exception report' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            reportorialIsUpdated: false
        }
    }

    componentDidMount() {
        const reportorial = this.props.client.servicingInstructions ? 
                           this.props.client.servicingInstructions.reportorial : 
                           undefined;
        this.reportorialData = this.reportorialData.map(data => {
            if (reportorial) {
                const reportorialData = reportorial.find(i => i.reportType === data.reportType);
                console.log(reportorialData);
                if (reportorialData) {
                    return {
                        reportType: data.reportType,
                        frequency: reportorialData.frequency,
                        available: reportorialData.available
                    }
                }
            }
            return data;
        });

        this.setState({reportorialIsUpdated: true});
    }

    render() : JSX.Element {

        return (
            <div className='sub-section'>
                <CustomTable
                    className='custom-table'
                    rows={this.reportorialData}
                    renderHeader={() => this.renderTableHeader()}
                    renderRow={(rowData: ReportorialData, index: number) => 
                        this.renderTableRow(rowData, index)} />
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        
        return (
            <>
            <TableCell>Report Type</TableCell>
            <TableCell>Frequency</TableCell>
            <TableCell style={{textAlign: 'center'}}>Available?</TableCell>
            </>
        );
    }

    renderTableRow(field: ReportorialData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell>{GlobalFunction.capitalizeEachWord(field.reportType)}</TableCell>
                <TableCell>{GlobalFunction.capitalizeEachWord(field.frequency)}</TableCell>
                <TableCell style={{textAlign: 'center'}}>{field.available ? 'Yes' : 'No'}</TableCell>
            </TableRow>
        );
    }
}