import * as React from 'react';
import { Grid, TableRow, TableCell } from '@material-ui/core';
import { ClientData, DocumentData } from 'Pages/CustomerCarePage/Models';
import { TextTab } from 'Pages/CustomerCarePage/Components/TextTab';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';

interface IProps {
    client: ClientData;
}

interface IStates {
    selectedTab: number;
    documents?: DocumentData[];
}

export class DocumentsSection extends React.Component<IProps, IStates> {

    tabList = [
        { value: 1, label: 'Signed Contracts' },
        { value: 2, label: 'Signed Proposals' },
        { value: 3, label: 'Masterlists' },
        { value: 4, label: 'APD Endorsements' },
        { value: 5, label: 'Corporate Guarantees' },
        { value: 6, label: 'Letter and Requests' }
    ];

    signedContractsHeader = [
        { label: 'Document Name',   state: 'name' },
        { label: 'Version',         state: 'version' },
        { label: 'Date Signed',     state: 'signedDate' },
        { label: 'Files',           state: 'files' },
        { label: 'Uploaded Date',   state: 'uploadDate' },
        { label: 'Uploaded By',     state: 'uploader' },
        { label: 'Action',          state: 'action' }
    ];

    signedProposalsHeader = [
        { label: 'Proposal Number', state: 'proposalNo' },
        { label: 'Version',         state: 'version' },
        { label: 'Date Signed',     state: 'signedDate' },
        { label: 'Files',           state: 'files' },
        { label: 'Uploaded Date',   state: 'uploadDate' },
        { label: 'Uploaded By',     state: 'uploader' },
        { label: 'Action',          state: 'action' }
    ];

    masterlistsHeader = [
        { label: 'Document Name',   state: 'name' },
        { label: 'Batch Name',      state: 'batchName' },
        { label: 'Files',           state: 'files' },
        { label: 'Uploaded Date',   state: 'uploadDate' },
        { label: 'Uploaded By',     state: 'uploader' },
        { label: 'Action',          state: 'action' }
    ];

    apdEndorsementsHeader = [
        { label: 'Document Name',   state: 'name' },
        { label: 'Type',            state: 'type' },
        { label: 'Date Endorsed',   state: 'endorseDate' },
        { label: 'Files',           state: 'files' },
        { label: 'Uploaded Date',   state: 'uploadDate' },
        { label: 'Uploaded By',     state: 'uploader' },
        { label: 'Action',          state: 'action' }
    ];

    corporateGuaranteesHeader = [
        { label: 'Document Name',   state: 'name' },
        { label: 'Sender',          state: 'sender' },
        { label: 'Files',           state: 'files' },
        { label: 'Uploaded Date',   state: 'uploadDate' },
        { label: 'Uploaded By',     state: 'uploader' },
        { label: 'Action',          state: 'action' }
    ];

    lettersAndRequestsHeader = [
        { label: 'Document Name',   state: 'name' },
        { label: 'Files',           state: 'files' },
        { label: 'Uploaded Date',   state: 'uploadDate' },
        { label: 'Uploaded By',     state: 'uploader' },
        { label: 'Action',          state: 'action' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1
        };
    }

    componentDidMount() {
        this.loadData('signed_contracts');
    }

    componentWillUpdate(_props: IProps, state: IStates) {
        if (this.state.selectedTab != state.selectedTab) {
            if (state.selectedTab === 1) {
                this.loadData('signed_contracts');
            } else if (state.selectedTab === 2) {
                this.loadData('signed_proposals');
            } else if (state.selectedTab === 3) {
                this.loadData('masterlists');
            } else if (state.selectedTab === 4) {
                this.loadData('apd_endorsements');
            } else if (state.selectedTab === 5) {
                this.loadData('corporate_guarantees');
            } else if (state.selectedTab === 6) {
                this.loadData('letters_and_requests');
            }
        }
    }

    loadData = (type: 'signed_contracts' | 
                      'signed_proposals' | 
                      'masterlists' | 
                      'apd_endorsements' | 
                      'corporate_guarantees' | 
                      'letters_and_requests') => {
        const filter = {
            where: {
                section: type,
                client_id: this.props.client.id
            }
        };

        API.get(`documents?filter=${JSON.stringify(filter)}`,
            (response) => {
                console.log(response);
                this.setState({documents: Payloads.getDocuments(response.documents)});
            },
            (error) => {
                console.log(error);
            });
    }

    render() : JSX.Element {

        return (
            <Grid container spacing={2}>
                <div>
                    <TextTab
                        tabList={this.tabList}
                        selectedTab={this.state.selectedTab}
                        tabStyle={(_index: number) => {
                            return { padding: '0 1rem' };
                        }}
                        tabLabel={(index: number) => {
                            return (
                                <div style={{width: '100%'}}>
                                    <div className='header-3' style={{margin: '6px 0'}}>
                                        {this.tabList[index].label}
                                    </div>
                                </div>
                            );
                        }}
                        setSelectedTab={(value: number) => {
                            this.setState({selectedTab: value});
                        }} />
                </div>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    {this.state.documents ? (
                        this.state.selectedTab === 1 ? (
                            <CustomTable
                                rows={this.state.documents}
                                renderHeader={() => {
                                    return this.renderTableHeader(this.signedContractsHeader);
                                }}
                                renderRow={(rowData: any, index: number) => {
                                    return this.renderTableRow(rowData, index, this.signedContractsHeader);
                                }} />
                        ) : this.state.selectedTab === 2 ? (
                            <CustomTable
                                rows={this.state.documents}
                                renderHeader={() => {
                                    return this.renderTableHeader(this.signedProposalsHeader);
                                }}
                                renderRow={(rowData: any, index: number) => {
                                    return this.renderTableRow(rowData, index, this.signedProposalsHeader);
                                }} />
                        ) : this.state.selectedTab === 3 ? (
                            <CustomTable
                                rows={this.state.documents}
                                renderHeader={() => {
                                    return this.renderTableHeader(this.masterlistsHeader);
                                }}
                                renderRow={(rowData: any, index: number) => {
                                    return this.renderTableRow(rowData, index, this.masterlistsHeader);
                                }} />
                        ) : this.state.selectedTab === 4 ? (
                            <CustomTable
                                rows={this.state.documents}
                                renderHeader={() => {
                                    return this.renderTableHeader(this.apdEndorsementsHeader);
                                }}
                                renderRow={(rowData: any, index: number) => {
                                    return this.renderTableRow(rowData, index, this.apdEndorsementsHeader);
                                }} />
                        ) : this.state.selectedTab === 5 ? (
                            <CustomTable
                                rows={this.state.documents}
                                renderHeader={() => {
                                    return this.renderTableHeader(this.corporateGuaranteesHeader);
                                }}
                                renderRow={(rowData: any, index: number) => {
                                    return this.renderTableRow(rowData, index, this.corporateGuaranteesHeader);
                                }} />
                        ) : this.state.selectedTab === 6 ? (
                            <CustomTable
                                rows={this.state.documents}
                                renderHeader={() => {
                                    return this.renderTableHeader(this.lettersAndRequestsHeader);
                                }}
                                renderRow={(rowData: any, index: number) => {
                                    return this.renderTableRow(rowData, index, this.lettersAndRequestsHeader);
                                }} />
                        ) : null
                    ) : (
                        <Loading height='10rem' />
                    )}
                </div>
            </Grid>
        );
    }

    renderTableHeader(headers: { label: string, state: string }[]): JSX.Element {
        return (
            <>
            {headers.map(header => {
                return this.renderHeaderColumn(header.state, header.label);
            })}
            </>
        );
    }

    renderTableRow(document: DocumentData, index: number, headers: { label: string, state: string }[]): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const signedDate = document.signedDate ? GlobalFunction.toShortDateString(document.signedDate) : '---';
        const endorseDate = document.endorseDate ? GlobalFunction.toShortDateString(document.endorseDate) : '---';
        const uploadDate = GlobalFunction.toShortDateString(document.uploadDate);
        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                {this.renderTableCell(document.name || '---',       'name', headers)}
                {this.renderTableCell(document.proposalNo || '---', 'proposalNo', headers)}
                {this.renderTableCell(document.version || '---',    'version', headers)}
                {this.renderTableCell(document.batchName || '---',  'batchName', headers)}
                {this.renderTableCell(document.type || '---',       'type', headers)}
                {this.renderTableCell(document.sender || '---',     'sender', headers)}
                {this.renderTableCell(signedDate,                   'signedDate', headers)}
                {this.renderTableCell(endorseDate,                  'endorseDate', headers)}
                {this.renderTableCell('---',                        'files', headers)}
                {this.renderTableCell(uploadDate,                   'uploadDate', headers)}
                {this.renderTableCell(document.uploader,            'uploader', headers)}
                {this.renderTableCell('---',                        'action', headers)}
            </TableRow>
        );
    }

    renderTableCell(value: string, conditionState: string, headers: { label: string, state: string }[]): JSX.Element | null {
        return (
            headers.find(header => header.state === conditionState) ? (
                <TableCell>{value}</TableCell>
            ) : null
        )
    }

    renderHeaderColumn(attrName: string, headerText: string, width?: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width ? width : '150px'}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            // this.sortFacilities(attrName, true);
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        // @ts-ignore
                        this.setState({[attrName]: value});
                    }} />
            </TableCell>
        );
    }
}