import * as React from 'react';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { TextTab } from 'Pages/CustomerCarePage/Components/TextTab';
import { CardSortingSection } from './CardSortingSection';
import { CardPrintingSection } from './CardPrintingSection';
import { BillingSection } from './BillingSection';
import { EnrollmentSection } from './EnrollmentSection';
import { CollectionSection } from './CollectionSection';
import { AvailmentSection } from './AvailmentSection';
import { ReportorialSection } from './ReportorialSection';

interface IProps {
    client: ClientData;
}

interface IStates {
    selectedTab: number;
}

export class ServicingInstructionsSection extends React.Component<IProps, IStates> {

    tabList = [
        { value: 1, label: 'Sorting of Cards' },
        { value: 2, label: 'Printing of Cards' },
        { value: 3, label: 'Billing' },
        { value: 4, label: 'Enrollment' },
        { value: 5, label: 'Collection' },
        { value: 6, label: 'Availment' },
        { value: 7, label: 'Reportorial' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1
        };
    }

    render() : JSX.Element {

        return (
            <div>
                <div className='sub-section'>
                    <TextTab
                        tabList={this.tabList}
                        selectedTab={this.state.selectedTab}
                        tabStyle={(_index: number) => {
                            return { padding: '0 1rem' };
                        }}
                        tabLabel={(index: number) => {
                            return (
                                <div style={{width: '100%'}}>
                                    <div className='header-3' style={{margin: '6px 0'}}>
                                        {this.tabList[index].label}
                                    </div>
                                </div>
                            );
                        }}
                        setSelectedTab={(value: number) => {
                            this.setState({selectedTab: value});
                        }} />
                </div>
                {this.state.selectedTab === 1 ? (
                    <CardSortingSection client={this.props.client} />
                ) : this.state.selectedTab === 2 ? (
                    <CardPrintingSection client={this.props.client} />
                ) : this.state.selectedTab === 3 ? (
                    <BillingSection client={this.props.client} />
                ) : this.state.selectedTab === 4 ? (
                    <EnrollmentSection client={this.props.client} />
                ) : this.state.selectedTab === 5 ? (
                    <CollectionSection client={this.props.client} />
                ) : this.state.selectedTab === 6 ? (
                    <AvailmentSection client={this.props.client} />
                ) : this.state.selectedTab === 7 ? (
                    <ReportorialSection client={this.props.client} />
                ) : null}
            </div>
        );
    }
}