
import * as React from 'react';

import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { UpdateData, AccommodationData, DiagnosisData, PhysicianData, ProcedureData, ItemData } from '../UpdateTicketProps';
import { DropdownChoice } from 'Pages/CustomerCarePage/Models';

interface IProps {
    ticketNo: string;
    updates: UpdateData[];
    accommodations: AccommodationData[];
    diagnoses: DiagnosisData[];
    physicians: PhysicianData[];
    procedures: ProcedureData[];
    items: ItemData[];

    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: string;

    coverage: DropdownChoice;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate?: Date;
    setTabSelected: (value: number) => void;
}

export class SummarySection extends React.Component<IProps> {

    getTotalAccommodationCost = () => {
        let totalCost = 0;
        this.props.accommodations.map(accommodation => {
            totalCost += accommodation.costPerDay * accommodation.days;
        });
        return totalCost;
    }

    getTotalPhysicianCost = () => {
        let totalCost = 0;
        this.props.physicians.map(physician => {
            totalCost += physician.amount;
        });
        return totalCost;
    }

    getTotalProcedureCost = () => {
        let totalCost = 0;
        this.props.procedures.map(procedure => {
            totalCost += procedure.amount;
        });
        return totalCost;
    }

    getTotalItemCost = () => {
        let totalCost = 0;
        this.props.items.map(item => {
            totalCost += item.amount;
        });
        return totalCost;
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div className='header-1'>Review Summary</div>
                {this.renderSection('UPDATES', 'update', 2,
                    <CustomTable
                        className='custom-table'
                        rows={this.props.updates}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell>Remarks</TableCell>
                                <TableCell style={{width: '200px'}}>As of Date</TableCell>
                                <TableCell style={{textAlign: 'right', width: '200px'}}>Running Hospital Bill</TableCell>
                                </>
                            );
                        }}
                        renderRow={(update: UpdateData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    <TableCell>{update.remarks}</TableCell>
                                    <TableCell>{GlobalFunction.toCommonDateString(update.date)}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(update.hospitalBill, true)} PHP`}</TableCell>
                                </TableRow>
                            );
                        }} />
                )}
                
                {this.renderSection('ROOM ACCOMMODATIONS', 'room', 3, 
                    <CustomTable
                        className='custom-table'
                        rows={this.props.accommodations}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell>Room Type</TableCell>
                                <TableCell style={{textAlign: 'right', width: '200px'}}>No. of Days</TableCell>
                                <TableCell style={{textAlign: 'right', width: '300px'}}>Cost Per Day</TableCell>
                                </>
                            );
                        }}
                        renderRow={(accommodation: AccommodationData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    <TableCell>{accommodation.roomType}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{accommodation.days}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(accommodation.costPerDay, true)} PHP`}</TableCell>
                                </TableRow>
                            );
                        }} />,
                        this.getTotalAccommodationCost()
                )}

                {this.renderSection('DIAGNOSES', 'diagnosis', 4, 
                    <CustomTable
                        className='custom-table'
                        rows={this.props.diagnoses}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell style={{width: '300px'}}>Diagnosis</TableCell>
                                <TableCell>Remarks</TableCell>
                                <TableCell style={{textAlign: 'center', width: '100px'}}>Type</TableCell>
                                <TableCell style={{textAlign: 'center', width: '175px'}}>Primary Diagnosis?</TableCell>
                                </>
                            );
                        }}
                        renderRow={(diagnosis: DiagnosisData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    <TableCell>{`${diagnosis.diagnosis.value} - ${diagnosis.diagnosis.label}`}</TableCell>
                                    <TableCell>{diagnosis.remarks}</TableCell>
                                    <TableCell style={{textAlign: 'center'}}>{diagnosis.isFinal ? 'Final' : 'Initial'}</TableCell>
                                    <TableCell style={{textAlign: 'center'}}>{diagnosis.isPrimary ? 'Yes' : 'No'}</TableCell>
                                </TableRow>
                            );
                        }} />
                )}
                
                {this.renderSection('PHYSICIANS', 'physicians', 5, 
                    <CustomTable
                        className='custom-table'
                        rows={this.props.physicians}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell style={{width: '250px'}}>Specialization - Doctor Name</TableCell>
                                <TableCell style={{width: '100px'}}>Diagnosis</TableCell>
                                <TableCell>Remarks</TableCell>
                                <TableCell style={{textAlign: 'right', width: '125px'}}>No. of Visits</TableCell>
                                <TableCell style={{textAlign: 'right', width: '200px'}}>Amount</TableCell>
                                </>
                            );
                        }}
                        renderRow={(physician: PhysicianData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    {physician.physician.params? (
                                        <TableCell>
                                            <div className='text'>{`${physician.physician.params[0]} - ${physician.physician.label}`}</div>
                                            <div className='light-text'>{`${physician.physician.params[1]} Physician`}</div>
                                        </TableCell>
                                    ) : ''}
                                    <TableCell>{physician.diagnosis.value}</TableCell>
                                    <TableCell>{physician.remarks}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{physician.visits}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(physician.amount, true)} PHP`}</TableCell>
                                </TableRow>
                            );
                        }} />,
                        this.getTotalPhysicianCost()
                )}
                
                {this.renderSection('PROCEDURES', 'procedures', 6,
                    <CustomTable
                        className='custom-table'
                        rows={this.props.procedures}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell style={{width: '200px'}}>Procedure</TableCell>
                                <TableCell style={{width: '100px'}}>Diagnosis</TableCell>
                                <TableCell style={{width: '175px'}}>Doctor</TableCell>
                                <TableCell style={{width: '150px'}}>Date</TableCell>
                                <TableCell style={{minWidth: '100px'}}>Remarks</TableCell>
                                <TableCell style={{textAlign: 'right', width: '150px'}}>Amount</TableCell>
                                </>
                            );
                        }}
                        renderRow={(procedure: ProcedureData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    <TableCell>{`${procedure.procedure.value} - ${procedure.procedure.label}`}</TableCell>
                                    <TableCell>{procedure.diagnosis.value}</TableCell>
                                    <TableCell>{procedure.physician.label}</TableCell>
                                    <TableCell>{GlobalFunction.toCommonDateString(procedure.date)}</TableCell>
                                    <TableCell>{procedure.remarks}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(procedure.amount, true)} PHP`}</TableCell>
                                </TableRow>
                            );
                        }} />,
                        this.getTotalProcedureCost()
                )}
                
                {this.renderSection('OTHER DETAILS', 'item', 8, 
                    <CustomTable 
                        className='custom-table'
                        rows={this.props.items}
                        renderHeader={() => {
                            return (
                                <>
                                <TableCell>Item</TableCell>
                                <TableCell style={{width: '250px'}}>Type</TableCell>
                                <TableCell style={{textAlign: 'right', width: '250px'}}>Amount</TableCell>
                                </>
                            );
                        }}
                        renderRow={(item: ItemData, index: number) => {
                            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                            return (
                                <TableRow style={{backgroundColor: backgroundColor}}>
                                    <TableCell>{item.name}</TableCell>
                                    <TableCell>{item.type.label}</TableCell>
                                    <TableCell style={{textAlign: 'right'}}>{`${GlobalFunction.formatNumber(item.amount, true)} PHP`}</TableCell>
                                </TableRow>
                            );
                        }} />,
                        this.getTotalItemCost()
                )}
                
                <div className='sub-section'>
                    <Grid container>
                        <Grid item xs={4}>
                            <div className='header-2'>PHILHEALTH</div>
                            <div className='sub-section'>
                                <div className='light-text'>COVERAGE</div>
                                <div className='small-text'>{this.props.coverage.label}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>AMOUNT</div>
                                <div className='small-text'>{GlobalFunction.formatNumber(this.props.philhealthAmount, true) + " PHP"}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>TICKET NUMBER</div>
                                <div className='small-text'>{this.props.ticketNo}</div>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <div className='header-2'>CONFINEMENT</div>
                            <div className='sub-section'>
                                <div className='light-text'>PROVIDER</div>
                                <div className='small-text'>{this.props.provider.value + ' - ' + this.props.provider.label}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>DATE ADMITTED</div>
                                <div className='small-text'>{GlobalFunction.toCommonDateString(this.props.dateAdmitted)}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>KIND OF CONFINEMENT</div>
                                <div className='small-text'>{this.props.confinementKind}</div>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <div className='header-2'>DISCHARGE DETAILS</div>
                            <div className='sub-section'>
                                <div className='light-text'>FINAL HOSPITAL BILL AMOUNT</div>
                                <div className='small-text'>{GlobalFunction.formatNumber(this.props.finalBill, true) + ' PHP'}</div>
                            </div>
                            <div className='sub-section'>
                                <div className='light-text'>DISCHARGE DATE</div>
                                <div className='small-text'>
                                    {this.props.dischargeDate ? (
                                        GlobalFunction.toCommonDateString(this.props.dischargeDate)
                                    ) : '---'}
                                </div>
                            </div>
                        </Grid>
                        <Grid item xs={4}>
                            <TextButton 
                                label='Edit PhilHealth' 
                                onClick={() => {
                                    window.scroll(0, -window.pageYOffset);
                                    this.props.setTabSelected(7);
                                }}/>
                        </Grid>
                        <Grid item xs={4}>
                            <TextButton 
                                label='Edit Confinement Details' 
                                onClick={() => {
                                    window.scroll(0, -window.pageYOffset);
                                    this.props.setTabSelected(3);
                                }}/>
                        </Grid>
                        <Grid item xs={4}>
                            <TextButton
                                label='Edit Discharge Details' 
                                onClick={() => {
                                    window.scroll(0, -window.pageYOffset);
                                    this.props.setTabSelected(7);
                                }}/>
                        </Grid>
                    </Grid>
                </div>
                <div className='section' />
            </div>
        );
    }

    renderSection(headerName: string, miniText: string, tabIndex: number, table: JSX.Element, totalValue?: number): JSX.Element {
        return (
            <div className='sub-section'>
                <div style={{float: 'right'}}>
                    <TextButton 
                        label={`Add / Edit ${miniText}`} 
                        onClick={() => {
                            window.scroll(0, -window.pageYOffset);
                            this.props.setTabSelected(tabIndex);
                        }}/>
                </div>
                <div className='header-2'>{headerName}</div>
                {table}
                {totalValue ? (
                    <div className='sub-section'>
                        <div style={{float: 'right'}}>
                            <span className='small-text'>TOTAL AMOUNT</span>
                            <span style={{marginLeft: '1rem'}} />
                            <span className='header-2'>
                                {GlobalFunction.formatNumber(totalValue, true) + ' PHP'}
                            </span>
                        </div>
                </div>
                ) : null}
            </div>
        );
    }
}