import * as React from 'react';
import { TableCell, TableRow } from '@material-ui/core';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';

import { DropdownChoice, PartialTicketData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { faSort } from '@fortawesome/free-solid-svg-icons';
import { TicketInfoModal } from './TicketInfoModal';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    memberId?: string;
    clientId?: string;
}

interface IStates {
    urgency: number;
    status: string;
    ticketNo: string;
    channel: string;
    type: string;
    specificType: string;
    source: string;
    assignee: string;
    lastUpdate: string;
    ticketListUpdated: boolean;
    selectedTicketId?: string;
}

export class ReviewTicketsSection extends React.Component<IProps, IStates> {

    ticketChannels: DropdownChoice[];
    ticketTypes: DropdownChoice[];
    specificTypes: DropdownChoice[];
    tickets: PartialTicketData[];

    constructor(props: any) {
        super(props);

        this.ticketChannels = [
            { value: '0', label: '' },
            { value: '1', label: 'Call' },
            { value: '2', label: 'SMS' },
            { value: '3', label: 'Text' },
            { value: '4', label: 'Email' }
        ];

        this.ticketTypes = [
            { value: '0', label: '' },
            { value: '1', label: 'Availment' },
            { value: '2', label: 'Inquiry' }
        ];

        this.specificTypes = [
            { value: '0', label: '' },
            { value: '1', label: 'Outpatient Procedure' },
            { value: '2', label: 'Inpatient' },
            { value: '3', label: 'Emergency Room' },
            { value: '4', label: 'Consultation' }
        ];

        this.state = {
            urgency: 0,
            status: '',
            ticketNo: '',
            channel: '',
            type: '',
            specificType: '',
            source: '',
            assignee: '',
            lastUpdate: '',
            ticketListUpdated: false,
            selectedTicketId: undefined
        }

        this.tickets = [];
    }

    componentDidMount() {
        const targetId = this.props.clientId || this.props.memberId;

        const filter = {
            where: {
                '$and': [
                    { '$or': [{ member_id: targetId }, { client_id: targetId }] },
                    { ticket_id: { '$ne': null }}
                ]
            }
        };

        API.get(`customer-care/tickets?filter=${JSON.stringify(filter)}`,
            (tickets) => {
                if (tickets) {
                    tickets.map(ticket => {
                        this.tickets.unshift({
                            id: ticket._id,
                            ticketNo: ticket.ticket_id,
                            urgency: (
                                ticket.ticket_category.urgency === 'Low' ? 3 :
                                ticket.ticket_category.urgency === 'Medium' ? 2 : 
                                ticket.ticket_category.urgency === 'High' ? 1 : 0),
                            status: ticket.status,
                            channel: ticket.received_via,
                            type: ticket.ticket_category.ticket_type,
                            specificType: ticket.ticket_category.specific_type,
                            source: {
                                value: ticket.received_from.toUpperCase(), 
                                label: (
                                    ticket.member_name ? ticket.member_name :
                                    ticket.corporate_client ? ticket.corporate_client :
                                    ticket.broker ? ticket.broker :
                                    ticket.hospital_name ? ticket.hospital_name : ''
                                )
                            },
                            assignee: ticket.assign_to,
                            lastUpdate: new Date(ticket.date_updated),
                        });
                    });
                    this.setState({ticketListUpdated: true});
                }
            },
            (error) => {
                console.log(error);;
            });
    }

    getFilteredTicketData = () => {
        return (
            this.tickets
                .filter(ticket => 
                    this.state.urgency == 0 || 
                    isNaN(this.state.urgency) ||
                    ticket.urgency == this.state.urgency)
                .filter(ticket => 
                    this.state.status === '' || 
                    ticket.status
                        .toLowerCase()
                        .includes(this.state.status.toLowerCase()))
                .filter(ticket => 
                    this.state.ticketNo === '' || 
                    ticket.ticketNo
                        .toLowerCase()
                        .includes(this.state.ticketNo.toLowerCase()))
                .filter(ticket => 
                    this.state.channel === '' || 
                    ticket.channel
                        .toLowerCase()
                        .includes(this.state.channel.toLowerCase()))
                .filter(ticket => 
                    this.state.type === '' || 
                    ticket.type
                        .toLowerCase()
                        .includes(this.state.type.toLowerCase()))
                .filter(ticket => 
                    this.state.specificType === '' || 
                    ticket.specificType
                        .toLowerCase()
                        .includes(this.state.specificType.toLowerCase()))
                .filter(ticket => 
                    this.state.source === '' || 
                    ticket.source.label
                        .toLowerCase()
                        .includes(this.state.source.toLowerCase()) ||
                    ticket.source.value
                        .toLowerCase()
                        .includes(this.state.source.toLowerCase()))
                .filter(ticket => 
                    this.state.assignee === '' || 
                    ticket.assignee
                        .toLowerCase()
                        .includes(this.state.assignee.toLowerCase()))
                .filter(ticket => 
                    this.state.lastUpdate === '' || 
                    GlobalFunction.toShortDateString(ticket.lastUpdate)
                        .toLowerCase()
                        .includes(this.state.lastUpdate.toLowerCase()))
        );
    }

    sortTickets(attrName: string, isAsc: boolean): void {
        this.tickets.sort((a: PartialTicketData, b: PartialTicketData) => 
            (a[attrName].toString().toLowerCase() > b[attrName].toString().toLowerCase()) ? 
                (isAsc ? 1 : -1) : (isAsc ? -1 : 1)
        );
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Review Tickets</div>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    {this.state.ticketListUpdated ? (
                        <CustomTable
                            className='custom-table-selectable'
                            rows={this.getFilteredTicketData()}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)}
                            onChangeInfo={(info: LabelDisplayedRowsArgs) => 
                                `${info.from}-${info.to} out of ${info.count} tickets`}
                            labelRowsPerPage='Show'
                            rowsPerPageOptions={[10, 25, 50]} />
                    ) : (
                        <Loading height='10rem'/>
                    )}
                </div>

                {this.state.selectedTicketId ? (
                    <TicketInfoModal 
                        ticketId={this.state.selectedTicketId}
                        onCloseButtonClicked={() => {
                            this.setState({selectedTicketId: undefined});
                        }} />
                ) : null}
                <div className='section' />
            </div>
        );
    }





    renderTableHeader(): JSX.Element {
        return (
            <>
            {this.renderHeaderColumn('urgency', 'Urgency', '75px')}
            {this.renderHeaderColumn('status', 'Status', '125px')}
            {this.renderHeaderColumn('ticketNo', 'Ticket ID')}
            {this.renderHeaderColumn('channel', 'Channel', '100px')}
            {this.renderHeaderColumn('type', 'Type', '100px')}
            {this.renderHeaderColumn('specificType', 'Specific Type', '200px')}
            {this.renderHeaderColumn('source', 'Source')}
            {this.renderHeaderColumn('assignee', 'Assigned')}
            {this.renderHeaderColumn('lastUpdate', 'Last Update')}
            </>
        );
    }





    renderTableRow = (ticket: PartialTicketData, index: number) => {
        const backgroundColor = (index % 2 === 0 ? (
                ticket.urgency === 1 ? '#FFF2F2' : '#FFFFFF'
            ) : (
                ticket.urgency === 1 ? '#F7ECEC' : '#F6F7FA'
            ));

        const urgencyColor = (
            ticket.urgency === 3 ? '#3AB77D' :
            ticket.urgency === 2 ? '#FAB435' :
            ticket.urgency === 1 ? '#FD5451' : '')
        
        return (
            <TableRow 
                style={{backgroundColor: backgroundColor}} 
                onClick={() => {
                    this.setState({selectedTicketId: ticket.id});
                }}>
                <TableCell style={{textAlign: 'center', paddingLeft: 0}}>
                    <div 
                        className='number-item' 
                        style={{display: 'inline-block', backgroundColor: urgencyColor}}>
                        {ticket.urgency}
                    </div>
                </TableCell>
                <TableCell>{ticket.status}</TableCell>
                <TableCell>{ticket.ticketNo}</TableCell>
                <TableCell>{ticket.channel}</TableCell>
                <TableCell>{ticket.type}</TableCell>
                <TableCell>{ticket.specificType}</TableCell>
                <TableCell>
                    <div className='text'>{ticket.source.label}</div>
                    <div className='light-text' style={{fontSize: '9px'}}>
                        {ticket.source.value}
                    </div>
                </TableCell>
                <TableCell>{ticket.assignee}</TableCell>
                <TableCell>{GlobalFunction.toDateAndTime(ticket.lastUpdate)}</TableCell>
            </TableRow>
        );
    }



    renderHeaderColumn(attrName: string, headerText: string, width?: string): JSX.Element {
        return (
            <TableCell style={{minWidth: width ? width : '150px'}}>
                <span className='header-2'>{headerText}</span>
                <div style={{float: 'right'}}>
                    <IconButton 
                        icon={faSort} 
                        onClick={() => {
                            this.sortTickets(attrName, true);
                            this.setState({ticketListUpdated: true});
                        }} />
                </div>
                <InputField
                    value={this.state[attrName]}
                    onChange={(value: string) => {
                        this.setState(prevState => ({...prevState, [attrName]: value}));
                    }} />
            </TableCell>
        );
    }
}


