import * as React from 'react';
import './component-style.scss';

interface IProps {
    placeholder: string;
    value: string;
    detail: string;
    onChange: (value: string) => void;
}

export class DetailedInputField extends React.Component<IProps> {

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.value !== props.value) ||
            (this.props.detail !== props.detail)
        );
    }

    render(): JSX.Element {
        return (
            <div className='detailed-input-field'>
                <div className='text'>{this.props.placeholder}</div>
                <div className='input-field'>
                    <input 
                        className='text-field left' 
                        value={this.props.value}
                        onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                            this.props.onChange(event.target.value);
                        }} />
                    <div className='detail right'>{this.props.detail}</div>
                </div>
            </div>
        );
    }
}