import * as React from 'react';
import { Grid, Paper } from '@material-ui/core';
import { ClientData, AddressData } from 'Pages/CustomerCarePage/Models';

interface IProps {
    client: ClientData;
}

interface IStates {
    
}

export class ContactsAndBranchesSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {

        };
    }

    render() : JSX.Element {
        const client = this.props.client;
        const mainAddress = client.mainOfficeAddress;
        const branches = client.branches;

        return (
            <div>
                {mainAddress ? (
                    this.renderAddress(mainAddress, 'Head Office')
                ) : null}
                <div style={{paddingBottom: '0.75rem'}} />
                {branches ? (
                    branches.map((branch, index) => {
                        return (
                            <>
                            {this.renderAddress(branch, `Branch ${index + 1}`)}
                            <div style={{paddingBottom: '0.75rem'}} />
                            </>
                        )
                    })
                ) : null}
            </div>
        );
    }

    renderAddress(address: AddressData, branchName: string): JSX.Element {
        
        return (
                <Paper className='section'>
                    <Grid container spacing={2}>
                        {this.renderItem('FLOOR / UNIT', branchName, 12, 'header-1', {
                            borderBottom: '#3C394A22 solid 1px',
                            padding: '8px'
                        })}
                        {this.renderItem('FLOOR / UNIT', `${address.floor} / ${address.unit}`)}
                        {this.renderItem('BUILDING', address.bldgName)}
                        {this.renderItem('STREET', address.street)}
                        {this.renderItem('BARANGAY', address.barangay)}
                        {this.renderItem('CITY / TOWN', address.city)}
                        {this.renderItem('REGION / ZIP CODE', `${address.region} / ${address.zipCode}`)}
                        {this.renderItem('TRUNKLINE / TEL. NO.', '', 6)}
                        
                        {address.contactPersons.length > 0 ? (
                            <>
                            {this.renderItem('CONTACT PERSON', undefined, 3, 'header-2', {padding: '16px 8px 0 8px'})}
                            {this.renderItem('POSITION', undefined, 3, 'header-2', {padding: '16px 8px 0 8px'})}
                            {this.renderItem('CONTACT NO.', undefined, 3, 'header-2', {padding: '16px 8px 0 8px'})}
                            {this.renderItem('CONTACT EMAIL', undefined, 3, 'header-2', {padding: '16px 8px 0 8px'})}
                            </>
                        ) : null}
                        
                        {address.contactPersons.map(contact => {
                            return (
                                <>
                                {this.renderItem('', contact.name, 3, 'header-2', {padding: '0 8px'})}
                                {this.renderItem('', contact.designation, 3, 'header-2', {padding: '0 8px'})}
                                {this.renderItem('', contact.contactNo, 3, 'header-2', {padding: '0 8px'})}
                                {this.renderItem('', contact.email, 3, 'header-2', {padding: '0 8px'})}
                                </>
                            );
                        })}
                    </Grid>
                </Paper>
        );
    }

    renderItem(label: string, value?: string | number, xs?: any, className?: string, style?: React.CSSProperties): JSX.Element {
        return (
            <Grid item className='sub-section' xs={xs ? xs : 3} style={{padding: '16px 8px', ...style}}>
                {label.length > 0 ? (
                    <div className='light-text'>{label}</div>
                ) : null}
                {value ? (
                    <div className={className ? className : 'header-2'}>{value}</div>
                ) : null}
            </Grid>
        )
    }
}