import * as React from 'react';
import './component-style.scss';
import { Checkbox } from '@material-ui/core';

interface IProps {
    placeholder?: string;
    label?: string;
    value: boolean;
    disabled?: boolean;
    onChange: (value: boolean) => void;
}

export class CustomCheckbox extends React.Component<IProps> {

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.value !== props.value)
        );
    }

    render(): JSX.Element {
        return (
            <div className='check-box-area'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                <Checkbox
                    className='check-box'
                    checked={this.props.value}
                    disabled={this.props.disabled}
                    onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                        this.props.onChange(event.target.checked);
                    }} />
                {this.props.label ? (
                    <span className='text'>{this.props.label}</span>
                ) : null}
            </div>
        );
    }
}