export class GlobalFunction {

    static formatNumber(num: number) {
        return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
      }


    static getIntials(text: string) {
        var initials = '';
        var wordArray = text.split(' ');
        for (var i = 0; i < wordArray.length; i++) {
            initials += wordArray[i].substring(0,1).toUpperCase() + '.';
        }
        return initials;
    }

    static toCommonDateString(date: Date) {
        if (date) {
            const monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
            ];
            const day = date.getDate();
            const monthIndex = date.getMonth();
            const year = date.getFullYear();
            return (monthNames[monthIndex] + " " + day + ", " + year);
        }

        return "";
    }

    static getAge(birthDate: any) {
        if (birthDate) {
            var ageInMillis = Date.now() - birthDate;
            return Math.abs(new Date(ageInMillis).getUTCFullYear() - 1970);
        }

        return null;
    }

    static getRandomValue() {
        return Math.random().toString(36).substr(2, 6);
    }
}