import * as React from 'react';
import { ClientData } from 'Pages/CustomerCarePage/Models';
import { Grid } from '@material-ui/core';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';

interface IProps {
    client: ClientData;
}

export class CollectionSection extends React.Component<IProps> {

    render() : JSX.Element {
        const collection = this.props.client.servicingInstructions ? 
                           this.props.client.servicingInstructions.collection : 
                           undefined;

        return (
            <div className='sub-section'>
                <Grid container spacing={2}>
                    <Grid item xs={6}>
                        <InputField
                            placeholder='Employee enrollment'
                            disabled
                            value={
                                collection && collection.employeeEnrollment ? 
                                collection.employeeEnrollment : ''}
                            onChange={(_value: string) => {}} />
                    </Grid>
                    <Grid item xs={6}>
                    <InputField
                            placeholder='Contact person for follow-ups'
                            disabled
                            value={
                                collection && collection.contactPersonForFollowups ? 
                                collection.contactPersonForFollowups : ''}
                            onChange={(_value: string) => {}} />
                    </Grid>
                    <Grid item xs={6}>
                        <div className='text'>Preferred means of payment</div>
                        <CustomRadio 
                            label='Bank Deposit'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'bank deposit' ? true : false}
                            onChange={() => {}} />
                        <Grid container>
                            <Grid item xs={1} />
                            <Grid item xs={5}>
                                <InputField
                                    placeholder='Preferred bank'
                                    disabled
                                    value={
                                        collection && collection.preferedBank ? 
                                        collection.preferedBank : ''}
                                    onChange={(_value: string) => {}} />
                            </Grid>
                        </Grid>
                        
                        <CustomRadio 
                            label='Pick-up check'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'pick-up check' ? true : false}
                            onChange={() => {}} />
                        <CustomRadio 
                            label='Deliver check'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'deliver check' ? true : false}
                            onChange={() => {}} />
                        <CustomRadio 
                            label='Pick-up cash'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'pick-up cash' ? true : false}
                            onChange={() => {}} />
                        <CustomRadio 
                            label='Deliver Cash'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'deliver cash' ? true : false}
                            onChange={() => {}} />
                        <CustomRadio 
                            label='Credit Card'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'credit card' ? true : false}
                            onChange={() => {}} />
                        <CustomRadio 
                            label='Wire Transfer'
                            disabled
                            checkedCondition={collection && collection.prefredMeansOfPayment === 'wire transfer' ? true : false}
                            onChange={() => {}} />
                    </Grid>
                    <Grid item xs={6}>
                        <InputField
                            placeholder='Details for payment pick-up'
                            disabled
                            value={
                                collection && collection.detialsForPaymentPickup ? 
                                collection.detialsForPaymentPickup : ''}
                            onChange={(_value: string) => {}} />
                    </Grid>
                </Grid>
            </div>
        );
    }
}