import * as React from 'react';
import { ConsultationDetails, DropdownChoice } from 'Pages/CustomerCarePage/Models';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';

interface IProps {
    consultation: ConsultationDetails;
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    icd10Codes: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshConsultationState: () => void;
}

export class ConsultationSection extends React.Component<IProps> {

    constructor(props: IProps) {
        super(props);
    }

    render(): JSX.Element {
        return (
            <div className='availment-section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>AVAILMENT DETAILS</div>
                    <DetailedDropdown 
                        choices={this.props.facilities}
                        placeholder='Provider'
                        value={this.props.consultation.provider}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.consultation.provider.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.consultation.provider = value;
                            this.props.refreshConsultationState();
                        }} />

                    <DetailedDropdown 
                        choices={this.props.icd10Codes}
                        placeholder='Diagnosis'
                        value={this.props.consultation.diagnosis}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.consultation.diagnosis.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.consultation.diagnosis = value;
                            this.props.refreshConsultationState();
                        }} />
                    <Dropdown 
                        choices={this.props.doctors}
                        placeholder='Doctor'
                        value={this.props.consultation.doctor}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.consultation.doctor.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.consultation.doctor = value;
                            this.props.refreshConsultationState();
                        }} />
                    <InputField
                        placeholder='Notes'
                        value={this.props.consultation.notes}
                        onChange={(value: string) => {
                            this.props.consultation.notes = value;
                            this.props.refreshConsultationState();
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }
}