    reset = () => {
        this.setState({
            loading: false,
            selectedTab: 0,
        });
    };

    setselectedTab = (tab: number) => {
        this.setState({
            selectedTab: tab,
        });
    };

    setLoading = (isLoading: boolean, callback: any = null) => {
        if(callback){
            this.setState({
                loading: isLoading,
            }, callback);
        }
        else{
            this.setState({
                loading: isLoading,
            });
        }
    };

    /******* handles changes in global search *******/
    alHandleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { value } = e.target;
        this.setState(
            {
                al_global_filter: value,
            },
            () => this.alHandleFilterChange(),
        );
    };

    /******* handles filters in table columns *******/
    alHandleFilterChange = () => {
        /** global filtering here */
    };

    fetchMemberDependents = (tempId: string, status: string, callback: any) => {
        let ins = this;

        if (tempId && tempId !== '' && status === 'Active') {
            const filter = JSON.stringify({
                where: {
                    principal_temp_id: tempId,
                    member_status: 'Active',
                },
            });
          
            const GET_DEPENDENTS_URL = `members?filter=${filter}`;

            API.get(GET_DEPENDENTS_URL,
            (response) => {
                if(response && response.length > 0){
                    ins.setState({
                        memberDependents: response
                    });
                }
                callback();
            },
            (error) => {
                console.log('get member dependents failed ', error);
                callback();
            });
        } else {
            callback();
        }
    };

    fetchMemberLogs = (memberId: string) => {
        let ins = this;

        API.get('member-logs?filter[order]=activity_id%20DESC&filter[where][member_id]=' + memberId,
            (memberLogsResponse) => {
                if(memberLogsResponse && memberLogsResponse.error === undefined && memberLogsResponse.length > 0){
                    let memberLogs = memberLogsResponse.map((item: any) => {
                        item['revision_time'] = item.revision_date;
                        return item;
                    });
                    ins.setState({
                        memberLogs: memberLogs,
                    });
                }
            },
            () => {
            }
        );
    };

    loadMemberData = () => {
        let ins = this;

        ins.setLoading(true, ()=> {
            API.get(`members?filter[where][member_id]=${ins.props.memberNumber}`,
            (memberDataList) => {
                if(memberDataList && memberDataList.error === undefined && memberDataList.length > 0){
                    let memberData = memberDataList[0];
                    ins.appendDocumentData(memberData.documents);
                    const { client_id, temp_id, member_status, plan_type } = memberData;
                    ins.setState({
                        memberData: memberData
                    });
                    ins.fetchMemberLogs(memberData._id);
                    ins.fetchMemberDependents(temp_id, member_status, () => {
                        API.get(`clients/account/hmo-info/${client_id}`,
                        (clienthmoinfo) => {
                            if(clienthmoinfo && clienthmoinfo.error === undefined){
                                const { client, benefit_plan_tree } = clienthmoinfo;
                                let contract_id = client && client.contracts && client.contracts[0] ? client.contracts[0] : '';
                                let contract_name = '';
                                let abl = '';
                                let mbl = '';
                                let pec = '';
                                let client_fields: any[] = [
                                    {
                                        "system_name": 'Corporate Acct. Name',
                                        "field_name": 'registered_name',
                                        "is_required": false,
                                        "input_type": 'text',
                                        "is_client_added": false,
                                        "is_editable": false,
                                    },
                                    {
                                        "system_name": 'Card Name',
                                        "field_name": 'name_on_card',
                                        "is_required": false,
                                        "input_type": 'text',
                                        "is_client_added": false,
                                        "is_editable": false,
                                    },
                                ];

                                if(client && client.member_data_fields && client.member_data_fields.length > 0) {
                                    client.member_data_fields.forEach(datafield => {
                                      if(datafield.field_type === 'Client-required'){
                                        let input_type = datafield.input_type === 'Date Field' ? 'date' :
                                          datafield.data_type === 'Numeric' ? 'number' : 'text';
                                        client_fields.push({
                                          "system_name": datafield.system_name,
                                          "field_name": datafield.field_name,
                                          "is_required": datafield.is_required,
                                          "input_type": input_type,
                                          "is_client_added": datafield.is_client_added,
                                          "is_editable": true,
                                        });
                                      }
                                    });
                                  }


                                if(benefit_plan_tree && benefit_plan_tree.length > 0){
                                    benefit_plan_tree.forEach(item => {
                                        if(item.tree_id === 'MCT'){
                                            contract_name = item.name ? item.name : '';
                                        }

                                        if(item.tree_id === 'MCT-CT-'+plan_type+'-RB'){
                                            let rbList = item['limits'];
                                            if(rbList.length>0){
                                                rbList.forEach((rblItem) => {
                                                    switch(rblItem['type']){
                                                    case 'ABL':
                                                        abl = ''+rblItem['limit_unit']+' '+GlobalFunction.formatNumber(rblItem['limit']);
                                                        break;
                                                    case 'MBL':
                                                        mbl = ''+rblItem['limit_unit']+' '+GlobalFunction.formatNumber(rblItem['limit']);
                                                        break;
                                                    case 'PEC':
                                                        pec = ''+rblItem['limit_unit']+' '+GlobalFunction.formatNumber(rblItem['limit']);
                                                        break;
                                                    default: 
                                                        break;
                                                    }
                                                });
                                            }
                                        }
                                    });
                                }

                                ins.setState({
                                    clientData: client,
                                    contractId: contract_id,
                                    contractName: contract_name,
                                    clientFields: client_fields,
                                    ABL: abl,
                                    MBL: mbl,
                                    PEC: pec,
                                }, () => {
                                    API.get(`members/documents/${memberData._id}`, (docResponse) => {
                                        if(docResponse && docResponse.error === undefined){
                                            ins.generateDocumentData(docResponse);
                                            ins.setLoading(false);
                                        }
                                        else{
                                            console.log('get member documents failed ', docResponse);
                                            ins.setLoading(false);
                                        }
                                    }, (docerror) => {
                                        console.log('get member documents failed ', docerror);
                                        ins.setLoading(false);
                                    });
                                });
                            }
                            else{
                                console.log('get client hmo info failed ', clienthmoinfo);
                                ins.setLoading(false);
                            }
                        }, 
                        (cerror) => {
                            console.log('get client hmo info failed ', cerror);
                            ins.setLoading(false);
                        });
                    });
                }
                else{
                    console.log('get member failed ', memberDataList);
                    ins.setLoading(false);
                }
            },
            (error) => {
                console.log('get member failed ', error);
                ins.setLoading(false);
            });
        });

        
    }

