
import * as React from 'react';
import { match } from "react-router-dom";
import { API } from '../API';
import { MemberData, TicketData, AvailmentDetails, LOAData, DropdownChoice, DiagnosisData, ConsultationDetails, ClientData, InquiryData, CommentData } from '../Models';
import { LeftPanelSection } from '../LeftPanelSection/LeftPanelSection';
import { CustomTabs } from '../Components/CustomTab';
import { Grid } from '@material-ui/core';
import { ApproveAvailmentSection } from './Components/ApproveAvailmentSection';
import { CheckBenefitSection } from './Components/CheckBenefitSection';
import { PartnerNetworkSection } from './Components/PartnerNetworkSection';
import { SummaryAvailmentSection } from './Components/SummaryAvailmentSection';
import { ReviewTicketsSection } from './Components/ReviewTicketsSection';
import { Payloads } from '../Payloads';
import { CheckUtilizationSection } from './Components/CheckUtilizationSection';
import { CheckContractSection } from './Components/CheckContractSection';
import { Processmaker } from '../Processmaker';
import { AccommodationData } from '../UpdateTicketPage/UpdateTicketProps';
import { ApproveInpatientSection } from './Components/ApproveInpatientSection';
import { Loading } from '../Components/Loading';
import { ApproveConsultationSection } from './Components/ApproveConsultationSection';
import { TicketFooter } from '../TicketFooter';
import { CustomModal } from '../Components/CustomModal';
import { CustomButton } from '../Components/CustomButton';
import { CommentSection } from './Components/CommentSection';
import { ViewInquirySection } from '../UpdateTicketPage/Components/ViewInquirySection';

interface IProps {
    required: string;
    match: match<MatchParams>;
    history: any;
}

interface MatchParams {
    id: string;
}

interface IStates {
    tabSelected: number;
    isSummarizing: boolean;

    member?: MemberData;
    client?: ClientData;
    ticket?: TicketData;

    availment?: AvailmentDetails;
    confinement?: ConfinementDetails;
    consultation?: ConsultationDetails;
    inquiries?: InquiryData[];
    comments?: CommentData[];

    pmId: string;
    loas: LOAData[];
    loaToDelete: string[];

    isCloseTicketModalOpen: boolean;
    otherActions?: HTMLButtonElement;
}

export interface ConfinementDetails {
    provider: DropdownChoice;
    dateAdmitted: Date;
    confinementKind: string;
    giftGiven: boolean;
    coverage: DropdownChoice;
    philhealthAmount: number;
    finalBill: number;
    dischargeDate?: Date;

    updates: any;
    others: any;
    diagnoses: DiagnosisData[];
    accommodations: AccommodationData[];
}

export class ResolveTicketPage extends React.Component<IProps, IStates> {

    tabList = [
        { value: 1, label: 'Availments' }, 
        { value: 2, label: 'Comments' }, 
        { value: 3, label: 'Benefits' }, 
        { value: 4, label: 'Partner Network' }, 
        { value: 5, label: 'Exclusion' }, 
        { value: 6, label: 'Utilization' }, 
        { value: 7, label: 'Tickets' },  
        { value: 8, label: 'Contract' }
    ];

    constructor(props: IProps) {
        super(props);

        this.state = {
            tabSelected: 1,
            isSummarizing: false,
            pmId: '',
            loas: [],
            loaToDelete: [],
            isCloseTicketModalOpen: false
        };
    }

    componentDidMount() {
        API.get('customer-care/tickets/' + this.props.match.params['ticketId'],
            (response) => {

                const ticketCategory = response.ticket_category;
                const availment = ticketCategory.availment_details;
                const confinement = ticketCategory.confinement_details;
                const consultation = ticketCategory.consultation_details;
                const inquiry = ticketCategory.inquiry_details;

                this.setState({ticket: Payloads.getTicketData(response)});

                if (availment) {
                    const providerData = availment.provider.split(' - ');
                    this.setState({availment: {
                        provider: { value: providerData[0], label: providerData[1] },
                        diagnoses: availment.availments_list[0].diagnosis.map(diagnosis => {
                            return {
                                icd10Code: { value: diagnosis.icd10_code, label: diagnosis.diagnosis },
                                remarks: diagnosis.remarks,
                                isPrimary: diagnosis.is_primary,
                                isFinal: diagnosis.is_final,
                                doctors: diagnosis.doctors.map(doctor => {
                                    return {
                                        id: doctor.id,
                                        name: doctor.name,
                                        specialization: doctor.specialization,
                                        fee: doctor.fee,
                                        noOfVisits: doctor.no_of_visits,
                                        remarks: doctor.remarks
                                    }
                                }),
                                procedures: diagnosis.procedures.map(procedure => {
                                    return {
                                        cptCode: { value: procedure.cpt_code, label: procedure.procedure_name },
                                        isPHIC: procedure.phic,
                                        amount: parseInt(procedure.amount),
                                        notes: procedure.notes,
                                        doctor: { value: '', label: procedure.doctor },
                                        dateAdded: new Date(procedure.date_added),
                                        assessmentType: procedure.assessment_type,
                                        assessmentRemarks: procedure.assessment_remarks,
                                        approvedAmount: procedure.approved_amount
                                    }
                                })
                            }
                        })
                    }});
                }

                if (confinement) {
                    const providerData = confinement.provider.split(' - ');
                    this.setState({confinement: {
                        provider: { value: providerData[0], label: providerData[1] },
                        dateAdmitted: new Date(confinement.date_admitted),
                        confinementKind: confinement.confinement_kind,
                        giftGiven: confinement.gift_given,
                        coverage: { value: '', label: confinement.coverage },
                        philhealthAmount: confinement.philhealth_amount,
                        finalBill: confinement.final_bill,
                        dischargeDate: confinement.discharge_date ? new Date(confinement.discharge_date) : undefined,
                        diagnoses: confinement.diagnoses.map(diagnosis => {
                            return {
                                icd10Code: { value: diagnosis.icd10_code, label: diagnosis.diagnosis },
                                remarks: diagnosis.remarks,
                                isPrimary: diagnosis.is_primary,
                                isFinal: diagnosis.is_final,
                                assessmentType: diagnosis.assessment_type,
                                assessmentRemarks: diagnosis.assessment_remarks,
                                approvedAmount: diagnosis.approved_amount,
                                doctors: diagnosis.doctors.map(doctor => {
                                    return {
                                        id: doctor.id,
                                        name: doctor.name,
                                        specialization: doctor.specialization,
                                        type: doctor.type,
                                        fee: doctor.fee,
                                        noOfVisits: doctor.no_of_visits,
                                        remarks: doctor.remarks
                                    }
                                }),
                                procedures: diagnosis.procedures.map(procedure => {
                                    return {
                                        cptCode: { value: procedure.cpt_code, label: procedure.procedure_name },
                                        isPHIC: procedure.phic,
                                        amount: parseInt(procedure.amount),
                                        notes: procedure.notes,
                                        doctor: procedure.doctor,
                                        dateAdded: new Date(procedure.date_added),
                                        assessmentType: procedure.assessment_type,
                                        assessmentRemarks: procedure.assessment_remarks,
                                        approvedAmount: procedure.approved_amount
                                    }
                                })
                            }
                        }),
                        accommodations: confinement.room_accommodation.map(accommodation => {
                            return {
                                roomType: accommodation.room_type,
                                days: parseInt(accommodation.no_of_days),
                                costPerDay: accommodation.amount
                            }
                        }),
                        updates: confinement.updates,
                        others: confinement.others
                    }});

                    this.tabList[0].label = 'Inpatient';
                }

                if (consultation) {
                    const providerData = consultation.provider.split(' - ');
                    const diagnosisData = consultation.diagnosis.split(' - ');
                    const doctorData = consultation.doctor.split(' - ');
                    this.setState({consultation: {
                        provider: { value: providerData[0], label: providerData[1] },
                        diagnosis: { value: diagnosisData[0], label: diagnosisData[1] },
                        doctor: { value: doctorData[0], label: doctorData[1] },
                        notes: consultation.notes,
                        assessmentType: consultation.assessment_type,
                        assessmentRemarks: consultation.assessment_remarks,
                        approvedAmount: consultation.approved_amount
                    }});
                    this.tabList[0].label = 'Consultation Details';
                }

                if (inquiry) {
                    this.setState({inquiries: inquiry.inquiry_list.map(value => {
                        return {
                            inquiry: { value: '', label: value.inquiry },
                            detail: value.value,
                            specificDetail: value.specific_detail,
                            answer: value.answer,
                            remarks: value.remarks,
                            relatedTickets: value.related_tickets,
                            supportDocs: value.support_docs
                        }
                    })});
                    this.tabList[0].label = 'Inquiry Details';
                }

                this.setState({comments: response.comments.map(comment => {
                    return {
                        sender: comment.user_name,
                        message: comment.comment,
                        date: new Date(comment.date_created)
                    }
                })});

                this.setState({loas: response.loa.map(loa => {
                    return {
                        id: loa._id,
                        loaNo: loa.loa_no,
                        amount: loa.amount,
                        date: new Date(loa.date),
                        notes: loa.notes
                    }
                })});

                if (response.member_id.trim().length > 0) {
                    API.get(`customer-care/members/${response.member_id}`, 
                        (response) => {
                            this.setState({member: Payloads.getMemberData(response)});
                            API.get(`customer-care/members/${response.member_id}/benefits`,
                                (response) => {
                                    console.log(response);
                                },
                                (error) => {
                                    console.log(error);
                                });
                        },
                        (error) => {
                            console.log(error);
                        });
                } else if (response.client_id.length > 0) {
                    API.get(`customer-care/clients/${response.client_id}`,
                        (response) => {
                            this.setState({client: Payloads.getClientData(response)});
                        },
                        (error) => {
                            console.log(error);
                        });
                }
            },
            (error) => {
                console.log(error);
            });
    }

    onUpdateAssessment = () => {

        if ((!this.state.member && !this.state.client) || !this.state.ticket) {
            return;
        }

        const ticket = this.state.ticket;

        const payload = Payloads.getPutUpdateAssessmentPayload(
            this.state.ticket,
            this.state.loas,
            this.state.member,
            this.state.client,
            this.state.availment,
            this.state.confinement,
            this.state.consultation
        );
        
        API.delete('customer-care/tickets/loa', this.state.loaToDelete,
            (response) => {
                console.log(response);
                API.put(`customer-care/tickets/${this.props.match.params['ticketId']}`, payload,
                    (response) => {
                        console.log(response);
                        ticket.approvalCode = response.approval_code;
                        this.setState({ticket: ticket});
                        window.location.reload();
                    },
                    (error) => {
                        console.log(error);
                    });
            },
            (error) => {
                console.log(error);
            });
    }

    onResolve = () => {

        const onResolveProcessmaker = () => {
            Processmaker.get(`cases/${this.props.match.params['caseId']}`,
                (response) => {                               
                    if (this.state.availment || this.state.confinement || this.state.consultation) {
                        if (response['current_task'][0]['tas_title'] === 'Approve / Disapprove Availment') {
                            processmakerRouteCase(() => {
                                returnToTicketList();
                            });
                        } else if (response['current_task'][0]['tas_title'] === 'Modify / Update Ticket') {
                            processmakerRouteCase(() => {
                                processmakerRouteCase(() => {
                                    returnToTicketList();
                                });
                            });
                        } else if (response['current_task'][0]['tas_title'] === 'Create Ticket') {
                            processmakerRouteCase(() => {
                                processmakerRouteCase(() => {
                                    processmakerRouteCase(() => {
                                        returnToTicketList();
                                    });
                                });
                            });
                        }
                    } else {
                        returnToTicketList();
                    }
                },
                (error) => {
                    console.log(error);
                }
            );
        }

        const processmakerRouteCase = (onSuccess: Function) => {
            Processmaker.put(`cases/${this.props.match.params['caseId']}/route-case`, {},
                (response) => {
                    console.log(response);
                    onSuccess();
                },
                (error) => {
                    console.log(error);
                }
            );
        }

        const returnToTicketList = () => {
            this.props.history.push('#/customer-care/tickets');
            window.location.reload();
        }

        if ((!this.state.member && !this.state.client) || !this.state.ticket) {
            return;
        }

        if (this.state.consultation) {
            const payload = Payloads.getPutUpdateAssessmentPayload(
                this.state.ticket,
                this.state.loas,
                this.state.member,
                this.state.client,
                this.state.availment,
                this.state.confinement,
                this.state.consultation,
                true
            )

            API.put(`customer-care/tickets/${this.props.match.params['ticketId']}`, payload,
                (response) => {
                    console.log(response);
                    onResolveProcessmaker();
                },
                (error) => {
                    console.log(error);
                });

            return;
        }

        API.patch(`customer-care/tickets/${this.props.match.params['ticketId']}`, { status: 'Resolved' },
                (patchTicketResponse) => {
                    console.log('Patch Response: ', patchTicketResponse);
                    onResolveProcessmaker();
                },
                (error) => {
                    console.log(error);
                });
                
        
    }

    allAvailmentIsDisapproved = () => {
        if (this.state.availment) {
            for (const diagnosis of this.state.availment.diagnoses) {
                for (const procedure of diagnosis.procedures) {
                    if (procedure.assessmentType !== 'Disapproved') {
                        return false;
                    }
                }
            }
        }
        if (this.state.confinement) {
            for (const diagnosis of this.state.confinement.diagnoses) {
                for (const procedure of diagnosis.procedures) {
                    if (procedure.assessmentType !== 'Disapproved') {
                        return false;
                    }
                }
            }
        }
        if (this.state.consultation) {
            if (this.state.consultation.assessmentType !== 'Disapproved') {
                return true;
            }
        }
        return true;
    }


    // @ts-ignore
    render(): JSX.Element {
        return (
            <Grid container className='customer-care-main'>
                {!this.state.ticket || (!this.state.member && !this.state.client) ? (
                    <div 
                        style={{
                            backgroundColor: '#00000055', 
                            zIndex: 100, 
                            position: 'fixed', 
                            width: '100%', 
                            height: '100%'}}>
                        <Loading height='15rem' size={100}/>
                    </div>
                    
                ) : (null)}

                <CustomModal
                    className='modal'
                    isOpen={this.state.isCloseTicketModalOpen}
                    handleClose={() => {
                        this.setState({isCloseTicketModalOpen: false});
                    }}
                    header='Resolve and Close Ticket'
                    content={
                        'Resolving and closing will no longer allow you to modify ' + 
                        'your approval/disapproval to this ticket. Are you sure ' +
                        'you want to resolve and close?'}
                    footer={
                        <>
                        <CustomButton
                            text='Cancel'
                            variant='outlined'
                            onClick={() => {
                                this.setState({isCloseTicketModalOpen: false});
                            }} />
                        <span style={{padding: '1rem'}} />
                        <CustomButton 
                            text='Resolve and Close'
                            onClick={() => {
                                this.setState({isCloseTicketModalOpen: false});
                                this.onResolve();
                            }} />
                        </>
                    } />

                <LeftPanelSection 
                    member={this.state.member}
                    client={this.state.client}
                    ticket={this.state.ticket}
                    task='Resolve'
                    disableLinkedTickets />

                <CustomTabs 
                    tabList={this.tabList}
                    selectedTab={this.state.tabSelected}
                    setSelectedTab={(index: number) => {
                        this.setState({tabSelected: index});
                    }} />

                <div className='main-content'>
                    {this.state.tabSelected === 1 && this.state.ticket ? (

                        this.state.availment ? (
                            this.renderOutpatientContent(this.state.availment, this.state.ticket)

                        ) : this.state.confinement ? (
                            this.renderInpatientContent(this.state.confinement, this.state.ticket)

                        ) : this.state.consultation ? (
                            this.renderConsultationContent(this.state.consultation)

                        ) : this.state.inquiries ? (
                            this.renderInquiryContent(this.state.inquiries)
                        ) : null
                    ) : this.state.tabSelected === 2 ? (
                        <CommentSection
                            ticketId={this.props.match.params['ticketId']}
                            comments={this.state.comments}
                            refreshComments={() => {
                                this.setState({comments: this.state.comments});
                            }} />
                    ) : this.state.tabSelected === 3 && this.state.member ? (
                        <CheckBenefitSection
                            memberId={this.state.member.memberId} />
                    ) : this.state.tabSelected === 4 ? (
                        <PartnerNetworkSection 
                            clientId={
                                this.state.member && this.state.member.client ? this.state.member.client.id : 
                                this.state.client ? this.state.client.id : ''} />
                    ) : this.state.tabSelected === 6 ? (
                        <CheckUtilizationSection />
                    ) : this.state.tabSelected === 7 ? (
                        <ReviewTicketsSection 
                            memberId={this.state.member ? this.state.member.memberId : undefined}
                            clientId={this.state.client ? this.state.client.corporateAccountNo: undefined} />
                    ) : this.state.tabSelected === 8 ? (
                        <CheckContractSection 
                            memberId={this.state.member ? this.state.member.memberId : undefined}
                            clientId={this.state.client ? this.state.client.corporateAccountNo : undefined} />
                    ) : null}
                </div>

                {this.state.ticket ? (
                    <TicketFooter
                        onSummarizeAssessment={
                            !this.state.ticket.approvalCode &&
                            !this.state.consultation && 
                            !this.state.isSummarizing &&
                            this.state.ticket.status !== 'Resolved' ? () => {
                                this.setState({isSummarizing: true});
                            } : undefined}
                        onGenerateApprovalCode={
                            !this.state.ticket.approvalCode && 
                            (this.state.consultation || this.state.isSummarizing) &&
                            !this.allAvailmentIsDisapproved() ? () => {
                                this.onUpdateAssessment();
                            } : undefined}
                        onResolveAndCloseTicket={
                            this.state.ticket.status !== 'Resolved' &&
                            (this.state.ticket.approvalCode ||
                            this.allAvailmentIsDisapproved()) ? () => {
                                this.setState({isCloseTicketModalOpen: true});
                            } : undefined}
                        // leftButtons={this.state.inquiries ? [
                        //     <CustomButton 
                        //         className='button'
                        //         text='Cancel Ticket'
                        //         disabled={true}
                        //         onClick={() => {}} />
                        // ] : undefined} 
                        />
                ) : null}
            </Grid>
        );
    }

    renderOutpatientContent(availment: AvailmentDetails, ticket: TicketData): JSX.Element {
        return (
            !this.state.isSummarizing && 
            !ticket.approvalCode &&
            ticket.status !== 'Resolved' ? (
                <ApproveAvailmentSection 
                    availment={availment}
                    refreshAvailmentState={() => {
                        this.setState({availment: this.state.availment});
                    }} />
            ) : (
                <SummaryAvailmentSection 
                    availment={availment}
                    loas={this.state.loas}
                    loaToDelete={this.state.loaToDelete}
                    canEditLOAs={!ticket.approvalCode && ticket.status !== 'Resolved'}
                    refreshLOAStates={() => {
                        this.setState({
                            loas: this.state.loas,
                            loaToDelete: this.state.loaToDelete
                        });
                    }} />
            )
        );
    }

    renderInpatientContent(confinement: ConfinementDetails, ticket: TicketData): JSX.Element {
        return (
            !this.state.isSummarizing && 
            !ticket.approvalCode &&
            ticket.status !== 'Resolved' ? (
                <ApproveInpatientSection
                    confinement={confinement}
                    refreshConfinementState={() => {
                        this.setState({confinement: this.state.confinement});
                    }} />
            
            ) : (
                <SummaryAvailmentSection 
                    availment={confinement}
                    loas={this.state.loas}
                    loaToDelete={this.state.loaToDelete}
                    canEditLOAs={!ticket.approvalCode}
                    refreshLOAStates={() => {
                        this.setState({
                            loas: this.state.loas,
                            loaToDelete: this.state.loaToDelete
                        });
                    }} />
            )
        );
    }

    renderConsultationContent(consultation: ConsultationDetails): JSX.Element {
        return (
            <div>
                <ApproveConsultationSection 
                    consultation={consultation}
                    refreshConsultationState={() => {
                        this.setState({consultation: this.state.consultation});
                    }} />
            </div>
        );
    }

    renderInquiryContent(inquiries: InquiryData[]): JSX.Element {
        return (
            <ViewInquirySection inquiries={inquiries} />
        );
    }
}