/** select customize style to set background as transparent */
const cellSelectStyles = makeStyles({
	select: {
    backgroundColor: 'transparent',
  }
});



/** customization of material ui select for the page */
const Select = ({ id, value, items, onChange }: any) => {
	const cellSelectClasses = cellSelectStyles();
	return (
		<MaterialUI.Select
			id={id}
			classes={cellSelectClasses}
			value={value}
      onChange={onChange}
      disableUnderline
			style={{ 
        backgroundColor: 'transparent', 
        color: '#272E4C', 
        fontSize: '0.875rem' ,
        border: '1px solid #DDDFE3',
        padding: '2px 8px',
        borderRadius: 12,
      }}
			renderValue={(selected: any) => {
				if (selected != null) {
					const value: any = items.find((item: any) => {
						return item.value === selected;
					});
					if (value) {
						return value.name;
					} else {
						return '';
					}
				}
				return '';
			}}
		>
			{items.map((item: any) => (
				<MaterialUI.MenuItem style={{color: '#272E4C', fontSize: '0.875rem'}} key={item.value} value={item.value}>
					{item.name}
				</MaterialUI.MenuItem>
			))}
		</MaterialUI.Select>
	);
};

