  getOpenTickets = () => {
    if (this.state.role === 'pro') {
      const taskTrayPayload: any = {
        member: []
      };
  
      const filter = {
        where: {
          '$and': [
            { ticket_id: { '$ne': null }},
            { 'ticket_category.specific_type': 'Inpatient' },
            { status: 'Open' },
            { member_id: { '$ne': ' ' } }
          ]
        }
      };

      API.get(`customer-care/tickets?filter=${JSON.stringify(filter)}`, (response) => {
        response.map((ticket, index) => {

          // Caches
          const confinementDetails = ticket.ticket_category.confinement_details;
          const memberData = ticket.member_data;
          const dateAdmitted = confinementDetails ? new Date(confinementDetails.date_admitted) : new Date();
            
          this.state.openTicketList.unshift({
            id: ticket._id,
            ticket_no: ticket.ticket_id,
            case_uid: ticket.pmaker_case_uid,
            name: memberData ? 
              GlobalFunction.toNameFormat(
                memberData.last_name,
                memberData.middle_name,
                memberData.first_name
              ) : '---',
            date_admitted: dateAdmitted,
            hospital: confinementDetails ? confinementDetails.provider.split(' - ')[1] : '',
            no_of_days: calculateLengthOfStay(dateAdmitted),
            status: ticket.status.toLocaleLowerCase(),
            urgency: ticket.ticket_category.urgency.toLocaleLowerCase()
          });

          if (memberData && memberData._id) {
            taskTrayPayload.member.push({
              member_id: memberData._id,
              date_admitted: dateAdmitted
              // date_admitted: new Date(dateAdmitted.getTime() - (1000*60*60*24*1000)) // 1000 days ago (use only for testing)
            });
          }

          if ((index + 1) === response.length) {
            this.loadTaskTray(taskTrayPayload);
          }
        });
        this.setState({openTicketList: this.state.openTicketList});
      });
  
      const calculateLengthOfStay = (dateAdmitted: Date) => {
        const now = new Date();
        const dayTimeCount = (1000 * 60 * 60 * 24)
        return Math.floor((now.getTime() - dateAdmitted.getTime()) / dayTimeCount);
      }

    } else {
      Processmaker.get('cases', (response) => {
        const cases = response.filter(pmCase => pmCase.app_title.length > 5);
        cases.map(pmCase => {
          const appTitle = JSON.parse(pmCase.app_title);
          this.state.openTicketList.push({
            app_uid: pmCase.app_uid,
            id: appTitle.ticket_id,
            ticket_id: pmCase.app_number,
            due_date: appTitle.due_date,
            ticket_type_title: appTitle.ticket_type_title,
            name: appTitle.name
          });
        });
        this.setState({openTicketList: this.state.openTicketList});
      });
    }
  }










  loadTaskTray = (taskTrayPayload: any) => {
    API.post('customer-care/task-tray', JSON.stringify(taskTrayPayload),
    (response) => {
      console.log(response);
      response.map(member => {
        member.changes.map(change => {
          this.state.confinedPatientChangesList.push({
            id: change._id,
            name: member.member_name,
            description: 
              GlobalFunction.capitalizeEachWord(
                change.description.includes('member') &&
                change.description.includes('status') ?
                change.field_value : 
                change.description
              ),
            status: change.field_value.toLowerCase(),
            field_value: change.field_value,
          });
        });
      });
      this.setState({confinedPatientChangesList: this.state.confinedPatientChangesList});
    },
    (error) => {
      console.log(error);
    });
  }










  getUnassignedTickets = () => {
    Processmaker.get(`cases/unassigned`, (cases) => {
      cases.map(pmCase => {
        const appTitle = JSON.parse(pmCase.app_title);
        this.state.unassignedTicketList.unshift({
          id: appTitle.ticket_id,
          name: `${pmCase.app_number}: ${appTitle.ticket_type_title}`,
          name2: `${pmCase.app_number} - ${appTitle.ticket_type_title}`,
          app_uid: pmCase.app_uid
        });
        this.setState({unassignedTicketList: this.state.unassignedTicketList});
      });
    });
  }










  handleClaim = (ticketType: string, caseItem: any) => {
    if (ticketType === 'unassigned') {
      Processmaker.postclaim(`light/case/${caseItem.app_uid}/claim`, () => {
        const body = {
          assign_to: `${localStorage['first_name']} ${localStorage['last_name']}`
        };
        API.patch(`customer-care/tickets/${caseItem.id}`, body, () => {
          Processmaker.put(`cases/${caseItem.app_uid}/route-case`, {}, () => {
            Processmaker.put(`cases/${caseItem.app_uid}/execute-trigger/7569582035e2fc4b6c71d60000740292`, {}, (response) => {
              Processmaker.put(`cases/${caseItem.app_uid}/route-case`, {}, () => {
                if (response && response.status && response.status === 200) {
                  this.setState({
                    loading: false,
                    isModalOpen:true,
                    modalMessage: `Ticket No. ${caseItem.name2} was claimed successfully.`,
                    modalTitle:'Ticket Claimed',
                  });
                }
              });
            });
          });
        });
      }, (error) => {
        console.log(error);
      });
    }
  }

